import MOD_clientes from './clientes'
import MOD_inmuebles from './inmuebles'
import MOD_ubicaciones from './ubicaciones'
import MOD_trabajos from './trabajos'
import MOD_usuarios from './usuarios'
import MOD_autenticacion from './autenticacion'
import MOD_grupos from './grupos'
import MOD_notificaciones from './notificaciones'
import MOD_pagos from './recurso'
import proyecto from './proyecto'
import alerta from './alerta'
import beneficiario from './beneficiario'

import { INITIALISER_FUNCTION, LOGOUT_FUNCTION } from '@/store/constants'

const modules = {
  MOD_clientes,
  MOD_inmuebles,
  MOD_ubicaciones,
  MOD_trabajos,
  MOD_usuarios,
  MOD_autenticacion,
  MOD_grupos,
  MOD_notificaciones,
  MOD_pagos,
  proyecto,
  alerta,
  beneficiario
}

function getModulesWithAction(allModules, actionName) {
  return Object
    .keys(allModules)
    .filter((moduleKey) => {
      const { actions, namespaced } = allModules[moduleKey];
      if (namespaced !== true) return false;

      return Object
        .prototype
        .hasOwnProperty
        .call(actions, actionName);
    });
}

const modulesToInit = getModulesWithAction(modules, INITIALISER_FUNCTION);
const modulesWithLogoutAction = getModulesWithAction(modules, LOGOUT_FUNCTION);

export default modules;

export {
  modules,
  modulesToInit,
  modulesWithLogoutAction,
};