<template>
  <v-form ref="formulario" v-model="validacion" lazy-validation>
    <v-file-input
      @change="cargar"
      v-model="archivo"
      accept=".xlsx"
      label="Elige tu archivo"
      outlined
      dense
      :append-outer-icon="archivo ? 'mdi-cloud-upload' : 'mdi-send-off'"
      @click:clear="cleanInmueblesVerificados()"
      @click:append-outer="crearMultipleInmuebles()"
    />
  </v-form>
</template>

<script>
import readXlsxFile from "read-excel-file"
export default{
  data: () => ({
    archivo: null,
    index_cabeceras: 0,
    index_cabeceras_filtradas: {
      nombre_inmueble: null,
      clave_inmueble: null,
      direccion_inmueble: null,
      nombre_estado: null,
      nombre_municipio: null,
      nombre_localidad: null,
      sirh: null
    },
    validacion: false,
  }),
  computed:{
    cliente() { return this.$store.getters["MOD_clientes/getCliente"] },
    inmuebles(){ return this.$store.getters["MOD_inmuebles/getInmueblesVerificados"] },
    verificarIn() { return this.$store.getters["MOD_inmuebles/getVerificarInmuebles"] }
  },
  created(){
    this.$store.commit('MOD_inmuebles/limpiarInmuebles')
  },
  methods:{
    async cargar(){
      var inmuebles = []
      if (this.$refs.formulario.validate()) {
        await readXlsxFile(this.archivo).then((rows) => {
          this.buscarIndexCabecera(rows)
          rows[this.index_cabeceras].forEach((element, index) => {
            const nombre_cabecera = this.normalizarPalabra(element)
            this.compararNombreCabecera(nombre_cabecera, index)
          })
          for (var fila = this.index_cabeceras+1; fila < rows.length; ++fila){
            inmuebles.push({
                'cliente': this.cliente,
                'nombre': 
                rows[fila][this.index_cabeceras_filtradas.nombre_inmueble] != null
                  ? rows[fila][this.index_cabeceras_filtradas.nombre_inmueble]
                  : "n/a",
                'clave': 
                rows[fila][this.index_cabeceras_filtradas.clave_inmueble] != null
                  ? rows[fila][this.index_cabeceras_filtradas.clave_inmueble]
                  : "n/a",
                'sirh': 
                rows[fila][this.index_cabeceras_filtradas.sirh] != null
                  ? rows[fila][this.index_cabeceras_filtradas.sirh]
                  : "n/a",
                'direccion': 
                  rows[fila][this.index_cabeceras_filtradas.direccion_inmueble] != null
                  ? rows[fila][this.index_cabeceras_filtradas.direccion_inmueble]
                  : "n/a",
                'estado': 
                  rows[fila][this.index_cabeceras_filtradas.nombre_estado] != null
                  ? rows[fila][this.index_cabeceras_filtradas.nombre_estado]
                  : "n/a",
                'municipio': 
                  rows[fila][this.index_cabeceras_filtradas.nombre_municipio] != null
                  ? rows[fila][this.index_cabeceras_filtradas.nombre_municipio]
                  : "n/a",
                'localidad': 
                  rows[fila][this.index_cabeceras_filtradas.nombre_localidad] != null
                  ? rows[fila][this.index_cabeceras_filtradas.nombre_localidad]
                  : "n/a",          
            })
          }        
        })
        this.$store.commit("MOD_inmuebles/setVerificarInmuebles", inmuebles)
        await this.verificarInmuebles()
      }
    },
    async verificarInmuebles(){ 
      const {error, mensaje, estado} = await this.$store.dispatch("MOD_inmuebles/verificarInmuebles", this.verificarIn)
      this.$store.commit("alerta/addAlertas", { "estado": estado, "mensaje": mensaje, "error": error })
    },
    async crearMultipleInmuebles(){ 
      const {error, mensaje, estado} = await this.$store.dispatch("MOD_inmuebles/AXN_CrearInmuebles", this.inmuebles)
      this.$store.commit("alerta/addAlertas", { "estado": estado, "mensaje": mensaje, "error": error })
    },
    cleanInmueblesVerificados(){
      this.$store.commit("MOD_inmuebles/celanInmueblesVerificados")
    },
    buscarIndexCabecera(rows){
      for (var fila = 0; fila < rows.length; ++fila){
          var contador_null = 0
          for (var columna = 0; columna < rows[fila].length; ++columna){
            if(contador_null == 5) {break}
            else if(rows[fila][columna] == null){
              contador_null = contador_null + 1
            }
            else contador_null = 0
          }
          if(contador_null < 5){
            this.index_cabeceras = fila
            break
          }
        }
    },
    normalizarPalabra(element) {
      const acentos = { á: "a", é: "e", í: "i", ó: "o", ú: "u", Á: "A", É: "E", Í: "I", Ó: "O", Ú: "U",};
      if(element == null){ return "n/a"}
      else return element.split("").map((letra) => acentos[letra] || letra).join("").toString().toLowerCase(); // quita acentos y pasalo a , minisculas
    },
    compararNombreCabecera(nombre_cabecera, index){
      if (nombre_cabecera === 'nombre' || nombre_cabecera === 'nombre del inmueble') {
        this.index_cabeceras_filtradas.nombre_inmueble = index
      } else if(nombre_cabecera === 'cr' || nombre_cabecera === 'codigo' || nombre_cabecera === 'id'){
        this.index_cabeceras_filtradas.clave_inmueble = index
      } else if(nombre_cabecera === 'direccion' || nombre_cabecera === 'calle y numero'){
        this.index_cabeceras_filtradas.direccion_inmueble = index
      } else if(nombre_cabecera === 'edo.' || nombre_cabecera === 'estado'){
        this.index_cabeceras_filtradas.nombre_estado = index
      } else if(nombre_cabecera === 'municipio' || nombre_cabecera === 'municipio'){
        this.index_cabeceras_filtradas.nombre_municipio = index
      } else if(nombre_cabecera === 'ciudad' || nombre_cabecera === 'localidad'){
        this.index_cabeceras_filtradas.nombre_localidad = index
      } else if(nombre_cabecera === 'convive' || nombre_cabecera === 'sirh' || nombre_cabecera === 'convivencia'){
        this.index_cabeceras_filtradas.sirh = index
      }
    },
  }
}
</script>