<template>
  <v-snackbar v-model="snackbar" right top color="transparent">
    <v-alert
      v-for="(alerta, key) in alertas"
      :key="key"
      dismissible
      border="left"
      colored-border
      :type="tipoAlerta(alerta)"
    >
      <div v-if="alerta.error">
        <span v-if="alerta.error.response.data.message">{{alerta.error.response.data.message}} <br></span>
        <span v-else>{{alerta.error.response.data.error}} <br></span>
      </div>
      <div v-else>
        <span v-text="alerta.mensaje" />
      </div>
    </v-alert>
  </v-snackbar>
</template>

<script>
export default {
  data: () => ({
    snackbar: false,
  }),
  computed: {
    alertas() {return this.$store.getters["alerta/getAlertas"]; },
  },
  methods: {
    tipoAlerta({ estado, error }) {
      const status = error ? error.response.status : estado;
      switch (true) {
        case (status < 226):
          return "success";
        case (status < 451):
          return "warning";
        default:
          return "error";
      }
    },
  },
  watch: {
    alertas(){
      if(this.alertas.length){ this.snackbar = true } 
    },
    snackbar(nuevoValor) {
      if(!nuevoValor){ this.$store.commit("alerta/clearAleras") }
    },
  },
};
</script>
