<template>
  <v-card class="mx-auto" outlined>
    <v-card-text>
      <v-card-title class="grey white--text">
        Fondos necesarios: <v-spacer /><v-toolbar-title>MX$ {{total}}</v-toolbar-title>
      </v-card-title>
      <v-card-text>
        <v-form ref="formulario" v-model="valid" lazy-validation>
          <v-simple-table dense class="container">
            <template v-slot:default>
              <thead>
                <tr>
                  <th class="text-left">Concepto</th>
                  <th class="text-left">Proyecto</th>
                  <th class="text-left">Inmueble</th>
                  <th class="text-left">Importe</th>
                  <th class="text-left">Eliminar</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(peticion, index) in peticiones_verificadas" :key="peticion.trabajo" >
                  <td><b>{{ trabajos_seleccionados[index].concepto }}</b></td>
                  <td>{{ trabajos_seleccionados[index].proyecto }}</td>
                  <td>{{ trabajos_seleccionados[index].inmueble.nombre }}</td>
                  <td>
                    <br>
                    <v-text-field
                      v-if="!peticion.error"
                      v-model="peticion.monto"
                      label="Importe"
                      solo
                      dense
                      autofocus
                      prefix="$"
                      type="number"
                      placeholder="1.00"
                      step="0.1"
                      min="1"
                      :rules="[(v) => v>0 || 'Es necesario escribir el monto mayor a 0.00' ]"
                    />
                    <v-chip v-else class="error" outlined>{{peticion.error}}</v-chip>
                  </td>
                  <td><v-btn icon @click="confirmarEliminacion(index)"><v-icon color="error">mdi-trash-can</v-icon></v-btn>
                  </td>
                </tr>
              </tbody>
            </template>
          </v-simple-table>
        </v-form>
      </v-card-text>
    </v-card-text>
  </v-card>
</template>

<script>
export default {
  data: () => ({
    continuar_p3: true
  }),
  computed: {
    trabajos_seleccionados() { return this.$store.getters["MOD_trabajos/getTrabajosSeleccionados"]; },
    peticiones_verificadas() { return this.$store.getters["MOD_pagos/getPeticionesVerificadas"] },
    total() {
      var total = 0.0
      for(const peticion of this.peticiones_verificadas){
        total = peticion.monto ? Number(peticion.monto) + total : 0.0 + total
      }
      return total.toFixed(2)
    },
    valid: {
      get(){ return this.continuar_p3 },
      set(nuevoValor){
        this.$emit("eventoContinuar", { 'isValid_p2': nuevoValor, 'validacion': this.$refs.formulario})
      }
    }
  },
  methods: {
    cambiarMonto(peticion, index){
      this.$store.commit("MOD_pagos/setMontoPeticionesVerificadas", {'editar_peticion': peticion, 'editar_index': index})
    },
    confirmarEliminacion(index){
      this.$store.commit("MOD_trabajos/deleteSeleccionado", index)
      this.$store.commit("MOD_pagos/deleteVerificarPeticionesVerificadas", index)
    }
  }
};
</script>
