<template>
  <v-expansion-panels multiple>
    <v-expansion-panel>
      <v-expansion-panel-header>Administrador({{this.adm.length}}) </v-expansion-panel-header>
      <v-expansion-panel-content>
        <v-data-table
          :headers="encabezados"
          :items="adm"
          hide-default-footer
          :items-per-page="-1"
          class="elevation-1"
        >
          <template v-slot:top>
            <FormularioAsignarRolUsuario
              :dialog="dialog"
              :groups="groups"
              :idMiembro="idMiembro"
              :rolUsuarioEncontrado="rolUsuarioEncontrado"
              @establecerDialog = establecerDialog
            />
          </template>
          <template v-slot:item.actions="{ item }">
            <v-icon small @click="obtenerRolMiembro(item)">
              mdi-account-settings
            </v-icon>
          </template>
        </v-data-table>
      </v-expansion-panel-content>
    </v-expansion-panel>
    <v-expansion-panel>
      <v-expansion-panel-header>Coordinadores({{this.coor.length}}) </v-expansion-panel-header>
      <v-expansion-panel-content>
        <v-data-table
          :headers="encabezados"
          :items="coor"
          hide-default-footer
          :items-per-page="-1"
          class="elevation-1"
        >
          <template v-slot:top>
            <FormularioAsignarRolUsuario
              :dialog="dialog"
              :groups="groups"
              :idMiembro="idMiembro"
              :rolUsuarioEncontrado="rolUsuarioEncontrado"
              @establecerDialog = establecerDialog
            />
          </template>
          <template v-slot:item.actions="{ item }">
            <v-icon small @click="obtenerRolMiembro(item)">
              mdi-account-settings
            </v-icon>
          </template>
        </v-data-table>
      </v-expansion-panel-content>
    </v-expansion-panel>
    <v-expansion-panel>
      <v-expansion-panel-header>Coordinadores({{this.con.length}}) </v-expansion-panel-header>
      <v-expansion-panel-content>
        <v-data-table
          :headers="encabezados"
          :items="con"
          hide-default-footer
          :items-per-page="-1"
          class="elevation-1"
        >
          <template v-slot:top>
            <FormularioAsignarRolUsuario
              :dialog="dialog"
              :groups="groups"
              :idMiembro="idMiembro"
              :rolUsuarioEncontrado="rolUsuarioEncontrado"
              @establecerDialog = establecerDialog
            />
          </template>
          <template v-slot:item.actions="{ item }">
            <v-icon small @click="obtenerRolMiembro(item)">
              mdi-account-settings
            </v-icon>
          </template>
        </v-data-table>
      </v-expansion-panel-content>
    </v-expansion-panel>
    <v-expansion-panel>
      <v-expansion-panel-header>Coordinadores({{this.personal.length}}) </v-expansion-panel-header>
      <v-expansion-panel-content>
        <v-data-table
          :headers="encabezados"
          :items="personal"
          hide-default-footer
          :items-per-page="-1"
          class="elevation-1"
        >
          <template v-slot:top>
            <FormularioAsignarRolUsuario
              :dialog="dialog"
              :groups="groups"
              :idMiembro="idMiembro"
              :rolUsuarioEncontrado="rolUsuarioEncontrado"
              @establecerDialog = establecerDialog
            />
          </template>
          <template v-slot:item.actions="{ item }">
            <v-icon small @click="obtenerRolMiembro(item)">
              mdi-account-settings
            </v-icon>
          </template>
        </v-data-table>
      </v-expansion-panel-content>
    </v-expansion-panel>
  </v-expansion-panels>
</template>

<script>
import FormularioAsignarRolUsuario from '@/components/Usuario/FormularioAsignarRolUsuario.vue'

export default {
  data: () => ({
    dialog: false,
    idMiembro: 0,
    value: null,
    groups: null,
    rolUsuarioEncontrado: false,
    grupos_permisos: [],
    encabezados: [
      { text: 'Usuario', value: 'usuario_usuario' },
      { text: 'Nombre', value: 'nombre_usuario' },
      { text: 'Apellidos', value: 'apellido_usuario' },
      { text: 'Número de trabajos', value: '' },
      { text: 'Rol', value: 'actions' },
    ],
  }),
  components: {
    FormularioAsignarRolUsuario,
  },
  computed: {
    personal() {
      return this.$store.state.MOD_usuarios.lista_usuarios
    },
    adm() {
      return this.$store.state.MOD_usuarios.usuarios_roles
    },
    con() {
      return this.$store.state.MOD_usuarios.usuarios_roles2
    },
    coor() {
      return this.$store.state.MOD_usuarios.usuarios_roles3
    }
  },
  created() {
    this.ObtenerUsuarios()
    this.ObtenerAd()
    this.ObtenerCon()
    this.ObtenerCoo()
  },
  methods: {
    async obtenerRolMiembro({ id_usuario }) {
      this.idMiembro = id_usuario;
      const { idMiembro } = this;
      const { error } = await this.$store.dispatch(
        'MOD_grupos/AXN_ObtenerRolMiembro',
        {
          id: idMiembro,
        }
      );
      if (error) {
        this.dialog = true
        this.groups = null
        this.rolUsuarioEncontrado = false
        return 0;
      }
      this.dialog = true;
      this.groups = this.$store.getters['MOD_grupos/GET_idRolMiembro'];
      this.rolUsuarioEncontrado = true
    },
    async ObtenerUsuarios() {
      await this.$store.dispatch(
        'MOD_usuarios/getUsuarios'
      );
    },
    async ObtenerAd() {
      await this.$store.dispatch(
        'MOD_usuarios/AXN_ObtenerPersonalRol', {rol: 'Administrador'}
      );
    },
    async ObtenerCon() {
      await this.$store.dispatch(
        'MOD_usuarios/AXN_ObtenerPersonalRol', {rol: 'Consultor'}
      );
    },
    async ObtenerCoo() {
      await this.$store.dispatch(
        'MOD_usuarios/AXN_ObtenerPersonalRol', {rol: 'Coordinador'}
      );
    },

    establecerDialog(bool) {
      this.dialog=bool
    }
  },
};
</script>
