<template>
  <v-dialog persistent max-width="300">
    <template v-slot:activator="{ on, attrs }">
      <v-icon v-bind="attrs" v-on="on" class="mr-2" :color="color">
        {{icon}}
      </v-icon>
    </template>
    <template v-slot:default="dialog">
      <v-card class="mx-auto">
        <v-card-title class="text-h6">
          {{tipo_pago}}
        </v-card-title>
        <v-card-text>{{mensaje}}</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="error darken-1" text @click="cerrrarDialogo(dialog)">
            Cancelar
          </v-btn>
          <v-btn color="green darken-1" text @click="aceptarConfirmacion(dialog)">
            Aceptar
          </v-btn>
        </v-card-actions>
      </v-card>
    </template>
  </v-dialog>
</template>

<script>
export default {
  props: {
    id: { type: Number, default: 0 },
    indice: { type: Number, default: 0 },
    mensaje: { type: String, default: "" },
    tipo_pago: { type: String, default: "" },
    icon: { type: String, default: 'mdi-blank' },
    color: { type: String, default: 'primary' }
    // dialog: { type: Object, default:() => ({value:false})}
  },
  methods: {
    aceptarConfirmacion(dialog){
      dialog.value = false;
      this.$emit("emitConfirmacion", { 'confirmacion':true, 'id': this.id, 'indice': this.indice})
    },
    cerrrarDialogo(dialog) {
      dialog.value = false;
      this.$emit("emitConfirmacion", { 'confirmacion':false, 'id': this.id, 'indice': this.indice})
    },
  },
};
</script>