<template>
  <v-card class="mx-auto" outlined elevation="5">
    <v-card-title>
      Trabajos
      <v-spacer></v-spacer>
      <v-text-field
        v-model="search"
        append-icon="mdi-magnify"
        label="Buscar"
        single-line
        hide-details
        clearable
        dense
        filled
        outlined
        rounded
      />
    </v-card-title>
    <v-card-subtitle> {{ trabajos.length + (trabajos.length === 1 ? ' Trabajo' : ' Trabajos') }} </v-card-subtitle>
    <v-card-text>
      <v-responsive class="overflow-y-auto" max-height="500">
        <v-data-table
          class="elevation-5"
          :loading="isCargando"
          :headers="headers"
          :items="trabajos"
          item-key="clave_inmueble"
          :items-per-page="-1"
          loading-text="Cargando... por favor espere"
          :footer-props="{
            itemsPerPageText: 'Filas por página',
            itemsPerPageAllText: 'Todos',
            pageText: ' p - Tp de n',
          }"
          no-data-text="No hay trabajos pendientes mayores a 5 días."
          :search="search"
          @click:row="verTrabajo"
        >
          <template v-slot:top>
            <v-toolbar flat>
              <v-spacer />
              <v-btn
                v-if="isAsignarBtn"
                @click="$router.push({name: 'asignar-trabajos'})"
                :disabled="isInmuebles"
                class="success ma-1"
              >
                Asignar trabajo
                <v-icon right>mdi-file-account</v-icon>
              </v-btn>
              <filtrado-trabajos v-if="isFiltadoBtn"/>
            </v-toolbar>
          </template>
          <template v-slot:item.modified_date="{ item }">
            <v-chip :color="getColor(item.modified_date)" dark>
              {{ item.modified_date }}
            </v-chip>
          </template>
          <template v-slot:item.property_office="{item}">
            {{ item.property_office ? item.property_office.name : '' }} ({{ item.property_office ? item.property_office.property_key : '' }})
          </template>
        </v-data-table>
      </v-responsive>
    </v-card-text>
  </v-card>
</template>

<script>
import FiltradoTrabajos from '../../components/trabajo/Filtrado.vue'
export default {
  props: {
    isFiltadoBtn: { type: Boolean, default: false },
    isAsignarBtn: { type: Boolean, default: false }
  },
  data: () => ({
    search: "",
    estado: null,
    cliente: null,
    blue: 3,
    headers: [
      { value: "concept.project", text: "Proyecto", sortable: false },
      { value: "concept.name", text: "Concepto", sortable: false },
      { value: "status", text: "Status" },
      { value: "property_office", text: "Inmueble", sortable: false },
      { value: "property_office.client", text: "Cliente", sortable: false },
      { value: "assigned_user", text: "Asignado", sortable: false },
      { value: "area_user", text: "Responsable", sortable: false },
      { value: "created_date", text: "Creado", sortable: false },
      { value: "modified_date", text: "Modificado", sortable: true },
    ],
    menorIgualFecha: null,
    mayorIigualFecha: null,
  }),
  components:{
    'filtrado-trabajos': FiltradoTrabajos
  },
  computed: {
    trabajos() { return this.$store.getters["MOD_trabajos/getTrabajos"] },
    isCargando() { return this.$store.getters["MOD_trabajos/getCargando"] },
    isInmuebles() { return !this.$store.getters["MOD_inmuebles/getInmuebles"].length; },
  },
  methods: {
    holgura(modified_date) {
      var date1 = new Date(modified_date);
      var date2 = new Date();
      var datestring =
        date2.getFullYear() +
        "-" +
        (date2.getMonth() + 1) +
        "-" +
        date2.getDate();
      var date3 = new Date(datestring);
      var holgura = (date3.valueOf() - date1.valueOf()) / 86400000;
      if (this.blue < holgura) this.blue = holgura;
      return holgura;
    },
    getColor(modified_date) {
      if (this.holgura(modified_date) >= 5) return "red";
      else if (this.holgura(modified_date) >= 2) return "orange";
      else return "green";
    },
    verTrabajo(value) {
      this.$store.commit("MOD_trabajos/setTrabajo", value);
      this.$router.push({
        name: "De",
      });
    },
  },
};
</script>
