<template>
  <v-row justify="center">
    <v-col cols="8">
      <listar-trabajos :isAsignarBtn="true"/>
    </v-col>
  </v-row>
</template>

<script>
import ListarTrabajos from "../../components/trabajo/ListarTrabajos.vue";
export default {
  components: {
    "listar-trabajos": ListarTrabajos,
  },
  computed:{
    cliente() { return this.$store.getters["MOD_clientes/getCliente"] }
  },
  created(){
    this.inicializarTrabajosCliente()
  },
  methods:{
    async inicializarTrabajosCliente(){
      await this.$store.dispatch("MOD_trabajos/obtenerTrabajosCliente", {cliente: this.cliente.id})
    }
  }
};
</script>
