<template>
  <v-main>
    <v-row dense justify="center">
      <v-col cols="12" sm="8" md="8" lg="8" xl="8">
        <v-stepper v-model="e1">
          <v-stepper-header>
            <v-stepper-step :complete="e1 > 1" step="1">
              Paso 1 <br />
              Trabajos
            </v-stepper-step>

            <v-divider />

            <v-stepper-step :complete="e1 > 2" step="2">
              Paso 2 <br />
              Fondos
            </v-stepper-step>

            <v-divider />

            <v-stepper-step :complete="e1 > 3" step="3">
              Paso 3 <br />
              Datos general
            </v-stepper-step>
            <v-divider />
            <v-stepper-step :complete="e1 > 3" step="4">
              Paso 4 <br />
              Metodo pago
            </v-stepper-step>
            <v-divider />
            <v-stepper-step step="5">
              Paso 5 <br />
              Confirmacion
            </v-stepper-step>
          </v-stepper-header>

          <v-stepper-items>
            <v-stepper-content step="1">
              <tabla-trabajos-usuario
                :mostrar_seleccionados="true"
                :is_peticion="false"
              />
              <br />
              <v-btn
                color="success"
                block
                @click="verificarPeticiones()"
                :disabled="isTrabajosSeleccionados"
              >
                Continue
              </v-btn>
            </v-stepper-content>

            <v-stepper-content step="2">
               <v-btn color="warning" block @click="e1 = 1">
                Regresar
              </v-btn>
              <br />
              <formulario-solcitud-recursos @eventoContinuar="setValidP2"/>
              <br>
              <v-btn
                color="success"
                block
                :disabled="!isValid_p2"
                @click="e1=3"
              >
                Continue
              </v-btn>
            </v-stepper-content>

            <v-stepper-content step="3">
              <v-btn color="warning" block @click="e1 = 2">
                Regresar
              </v-btn>
              <br>
              <formulario-crear-recurso @eventoContinuar="setValidP3"></formulario-crear-recurso>
              <br>
              <v-btn color="success" block @click="e1 = 4" :disabled="!isValid_p3">
                Continuar
              </v-btn>
            </v-stepper-content>

            <v-stepper-content step="4">
              <v-btn color="warning" block @click="e1 = 3">
                Regresar
              </v-btn>
              <br>
              <metodo-pago @eventoContinuar="setValidP4"/>
              <br>
              <v-btn color="success" block @click="e1 = 5" :disabled="!isValid_p4">
                Continuar
              </v-btn>
            </v-stepper-content>

            <v-stepper-content step="5">
              <v-btn color="warning" block @click="e1 = 4">
                Regresar
              </v-btn>
              <br>
              <crear-recurso />
              <br>
              <v-btn color="primary" block @click="crearSolicitudRecurso()">
                Finalizar 
              </v-btn>
            </v-stepper-content>
          </v-stepper-items>
        </v-stepper>
      </v-col>
      <v-col cols="12" sm="2" md="2" lg="2" xl="2">
        <v-card class="mx-auto" outlined>
          <v-toolbar dense elevation="1"><v-toolbar-title>Resumen de la solicitud de recursos </v-toolbar-title></v-toolbar>
          <v-card-text>
            Trabajos: ... <br>
            Innmuebles: ... <br>
            Fecha: {{getFechaMod()}} <br>
            Solicitudes: {{solicitud_recurso.multiple ? 1 : verificar_peticiones.length}} <br>
            Tipo: {{solicitud_recurso.tipo_pago ? solicitud_recurso.tipo_pago : 'n/a'}}
          </v-card-text>
          <v-divider  />
          <v-card-title>Total: {{ total() }}</v-card-title>
          <div class="text-end">
            <v-btn rounded color="error" @click="$router.go(-1)">
              Cancelar
              <v-icon right>mdi-close-circle-outline</v-icon>
            </v-btn>
          </div>
        </v-card>
      </v-col>
    </v-row>
  </v-main>
</template>

<script>
import TablaTrabajos from '../../components/trabajo/ListarTrabajosUsuario.vue'
import FormularioPeticiones from '../../components/Pago/FormularioPeticiones.vue'
import FormularioSolicitudRecuro from '../../components/Pago/FormularioRecurso.vue'
import MetodoPago from '../../components/Seleccion/MetodosPago.vue'
import CrearRecurso from '../../components/Pago/CrearRecurso.vue'

export default {
  data: () => ({
    e1: 1,
    mostrar_seleccionados: true,
    isValid_p2: false,
    isValid_p3: false,
    isValid_p4: false
  }),
  components: {
    "tabla-trabajos-usuario": TablaTrabajos,
    "formulario-solcitud-recursos": FormularioPeticiones,
    "formulario-crear-recurso": FormularioSolicitudRecuro,
    "metodo-pago": MetodoPago,
    "crear-recurso": CrearRecurso
  },
  computed: {
    trabajos() { return this.$store.getters["MOD_trabajos/getTrabajos"]; },
    isTrabajosSeleccionados() { return this.$store.getters["MOD_trabajos/getTrabajosSeleccionados"].length ? false : true },
    verificar_peticiones(){return this.$store.getters["MOD_pagos/getVerificarPeticiones"]},
    isPeticiones_verificadas() { return this.$store.getters["MOD_pagos/getPeticionesVerificadas"].length ? true : false },
    solicitud_recurso(){ return this.$store.getters["MOD_pagos/getSolicitudRecurso"] },
  },
  methods:{
    setValidP2({validacion}){
      this.isValid_p2 = validacion ? validacion.validate() : false,
      this.isValid_p2 = this.isValid_p2 ? this.isPeticiones_verificadas : this.isValid_p2
    },
    setValidP3({validacion}){
      this.isValid_p3 = validacion ? validacion.validate() : false
    },
    setValidP4({validacion}){
      this.isValid_p4 = validacion ? validacion.validate() : false
    },
    async verificarPeticiones() {
      const { mensaje, estado, error } = await this.$store.dispatch("MOD_pagos/verificarPeticiones", this.verificar_peticiones)
      this.$store.commit("alerta/addAlertas", { "estado": estado, "mensaje": mensaje, "error": error })
      this.e1 = 2
    },
    getFechaMod(){
      let date= new Date();
      return date.toLocaleDateString();
    },
    total() {
      var total = 0.0
      for(const peticion of this.$store.getters["MOD_pagos/getPeticionesVerificadas"]){
        total = peticion.monto ? Number(peticion.monto) + total : 0.0 + total
      }
      return total.toFixed(2)
    },
    async crearSolicitudRecurso(){
      const {mensaje, estado, error} = await this.$store.dispatch("MOD_pagos/crearRecurso")
      if(!error){
        this.$store.commit("MOD_trabajos/cleanSeleccionados")
        this.$router.push({ 'name': 'recursos-usuario' })
      }
      this.$store.commit("alerta/addAlertas", { "estado": estado, "mensaje": mensaje, "error": error })
    }
  }
}
</script>