<template>
  <v-list-item link>
    <v-dialog v-model="dialog" width="500">
      <template v-slot:activator="{ on, attrs }">
        <v-list-item-title v-bind="attrs" v-on="on">Crear nuevo inmueble</v-list-item-title>
      </template>
      <v-card class="mx-auto" outlined max-width="500">
        <v-toolbar elevation="1" dense>
          <v-toolbar-title>Crear nuevo inmueble</v-toolbar-title>
          <v-spacer />
            <v-btn icon fab color="error" @click="cerrarDialgo()">
              <v-icon>mdi-close</v-icon>
            </v-btn>
          </v-toolbar>
        <v-card-text>
          <v-form ref="formulario" v-model="validacion" lazy-validation>
              <!-- <v-autocomplete
                v-model="inmueble.cliente"
                label="Cliente"
                :rules="[(v) => !!v || 'Es necesario elejir un cliente']"
                item-text="nombre"
                :items="clientes"
                return-object
              /> -->

            <v-text-field
              v-model="inmueble.nombre"
              label="Nombre"
              :rules="true ? [(v) => !!v || 'Es necesario un nombre'] : []"
            />
            <v-text-field
              v-model="inmueble.clave"
              label="Clave"
              :rules="[(v) => !!v || 'Es necesario una clave']"
            />
            <v-autocomplete
              v-model="inmueble.estado"
              label="Estado"
              :rules="[(v) => !!v || 'Es necesario elejir un Estado']"
              item-text="nombre"
              :items="estados"
              return-object
              no-data-text="No se encontraron estados."
              clearable
              @change="inmueble.estado ? obtenerMunicipiosDeEstado(inmueble.estado): $store.commit('MOD_ubicaciones/limpiarMunicipios')"
            />
            <v-autocomplete
              v-model="inmueble.municipio"
              label="Municipio"
              :rules="[(v) => !!v || 'Es necesario elejir un Municipio']"
              :items="municipios"
              item-text="nombre"
              no-data-text="No se encontraron municipios."
              return-object
              clearable
            />
                <!-- @change="inmueble.municipio ? obtenerLocalidadesDeMunicipio(inmueble.municipio) : $store.commit('MOD_ubicaciones/limpiarLocalidades')" -->

            <v-autocomplete
              v-model="inmueble.localidad"
              label="Localidad"
              item-text="nombre"
              :items="localidades"
              return-object
              clearable
              no-data-text="No se encontraron localidades."
            />
            <v-textarea
              v-model="inmueble.direccion"
              label="Direccion"
              :rules="[(v) => !!v || 'Es necesario una direccion']"
              auto-grow
              rows="1"
              row-height="15"
            />
          </v-form>
        </v-card-text>
        <v-card-actions>
          <v-spacer />
          <v-btn @click="crearInmueble()" :disabled="!validacion" color="success" outlined>Agregar</v-btn>
          <v-btn @click="cerrarDialgo()" :disabled="!validacion" outlined color="error">Cerrar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-list-item>
</template>

<script>
export default {
  data: () => ({
    validacion: false,
    dialog: false
  }),
  computed: {
    inmueble() { return this.$store.getters["MOD_inmuebles/getInmueble"] },
    cliente() { return this.$store.getters["MOD_clientes/getCliente"]; },
    estados() { return this.$store.getters["MOD_ubicaciones/getEstados"]; },
    municipios() { return this.$store.getters["MOD_ubicaciones/getMunicipios"]; },
    isMunicipios() { return this.municipios.length ? false : true; },
    isCargandoMunuicipios() { return this.$store.getters["MOD_ubicaciones/getCargandoMunicipios"]; },
    localidades() { return this.$store.getters["MOD_ubicaciones/getLocalidades"]; },
  },
  created() {
    // this.inicializarEstados();
  },
  methods: {
    async inicializarEstados() {
      await this.$store.dispatch("MOD_ubicaciones/obtenerEstados");
    },
    async obtenerMunicipiosDeEstado({ id }) {
      await this.$store.dispatch("MOD_ubicaciones/obtenerMunicipiosDeEstado", id);
    },
    async obtenerLocalidadesDeMunicipio({ id }) {
      await this.$store.dispatch("MOD_ubicaciones/obtenerLocalidadesDeMunicipio", id);
    },
    async crearInmueble() {
      if (this.$refs.formulario.validate()) {
        this.inmueble.cliente = this.cliente.id
        const {mensaje, estado, error } = await this.$store.dispatch("MOD_inmuebles/crearInmueble", this.inmueble)
        this.$store.commit("alerta/addAlertas", { "estado": estado, "mensaje": mensaje, "error": error })
        if(!error){
          this.cerrarDialgo()
        }
      }
    },
    Limpiar() {
      this.$refs.formulario.reset();
    },
    cerrarDialgo(){
      this.dialog = false
      this.$refs.formulario.reset()
    }
  },
};
</script>
