<template>
  <v-dialog v-model="dialog" persistent max-width="800">
    <template v-slot:activator="{ on, attrs }">
      <v-btn class="ma-2" color="success" dark v-bind="attrs" v-on="on">
        <v-icon dark left> mdi-account-plus </v-icon> Agregar miembro
      </v-btn>
    </template>
    <v-card>
      <v-progress-linear color="info" :indeterminate="cargandoDatos" />
      <v-card-title>Agregar miembro a Systema ADVR</v-card-title>
      <v-form ref="form" v-model="valid" :disabled="cargandoDatos">
        <v-container>
          <v-row>
            <v-col cols="12" md="6">
              <v-text-field
                v-model="name"
                :rules="[(v) => !!v || 'Se requiere el nombre']"
                label="Nombre"
                required
              />
            </v-col>
            <v-col cols="12" md="6">
              <v-text-field
                v-model="last_name"
                label="Apellidos"
                :rules="[(v) => !!v || 'El apellido es obligatorio']"
                required
              />
            </v-col>
            <v-col cols="12" md="12">
              <v-text-field
                v-model="username"
                label="Nombre usuario"
                :rules="[(v) => !!v || 'Se requiere nombre de usuario']"
                required
              />
            </v-col>
            <v-col cols="12" md="6">
              <v-text-field
                v-model="email"
                label="Correo"
                :rules="[(v) => !!v || 'Correo electronico es requerido']"
              />
            </v-col>
            <v-col cols="12" md="6">
              <v-select
                v-model="dominio"
                :items="dominios"
                label="Dominios"
                required
              />
            </v-col>
            <v-col cols="12" md="12">
              <v-text-field
                v-model="password"
                :append-icon="mostrarContrasena ? 'mdi-eye' : 'mdi-eye-off'"
                :rules="[(v) => !!v || 'Requerido.']"
                :type="mostrarContrasena ? 'text' : 'password'"
                name="input-10-1"
                label="Contraseña"
                @click:append="mostrarContrasena = !mostrarContrasena"
              />
            </v-col>
            <v-col cols="12" md="6">
              <v-checkbox
                v-model="is_staff"
                label="¿Es un miembro del personal?"
              />
            </v-col>
          </v-row>
          <v-card-actions>
            <v-spacer />
            <v-btn @click="crearMiembro()" color="green">Agregar</v-btn>
            <v-btn @click="cerrar()" color="red" class="ma-5">Cerrar</v-btn>
          </v-card-actions>
        </v-container>
      </v-form>
    </v-card>
  </v-dialog>
</template>

<script>
// import newGroup from '../../components/groups/CreateNewGroup.vue'

export default {
  data: () => ({
    username: null,
    email: null,
    password: null,
    dominio: "@advr.mx",
    name: null,
    last_name: null,
    is_staff: true,
    groups: [],
    grupos_permisos: [],
    valid: true,
    dialog: false,
    dominios: ["@advr.mx", "@gesscorpzero.com"],
    mostrarContrasena: false,
    cargandoDatos: false,
  }),
  methods: {
    async crearMiembro() {
      if (this.$refs.form.validate()) {
        this.cargandoDatos = true
        const { username, email, dominio, password, name, last_name, is_staff, } = this;
        const { message, error } = await this.$store.dispatch(
          "MOD_usuarios/AUX_CrearUsuaurio",
          {
            email: email.concat(dominio),
            username: username,
            password: password,
            name: name,
            last_name: last_name,
            is_staff: is_staff,
          }
        );
        this.cargandoDatos = false
        if (error) {
          Object.getOwnPropertyNames(error).forEach(function(val) {
            alert(error[val]);
          });
        }
        alert(message);
      }
    },
    cerrar() {
      this.$refs.form.reset();
      this.$refs.form.resetValidation();
      this.dialog = false;
    },
  },
};
</script>
