<template>
  <v-card class="mx-auto" outlined>
    <v-card-title class="grey lighten-2">Seleccione un método de pago: <v-spacer></v-spacer>
    </v-card-title>
    <br>{{ otro_metodo_pago_seleccionado }}
    <v-card-text>
      <v-form v-model="valid" lazy-validation ref="formulario">
        <v-row dense>
          <v-col cols="12" sm="5">
            <v-combobox
              v-if="isMetodoPagoGenereal"
              v-model="solicitud_recurso.beneficiario"
              :items="beneficiarios"
              label="Nombre del beneficiario"
              item-text="nombre"
              :rules="[(v) => !!v || 'Es necesario elegir un beneficiario']"
              clearable
              clear-icon="mdi-close"
              return-object
              outlined
              @click:clear="solicitud_recurso.datos_bancarios=null, solicitud_recurso.metodo_pago=''"
            />
          </v-col>
          <v-col cols="2">
            <formulario-crear-beneficiario
              v-if="isMetodoPagoGenereal"/>
          </v-col>
          <v-col cols="12" sm="5" >
            <v-text-field
              v-if="solicitud_recurso.metodo_pago === 'Convenio'"
              v-model="convenio"
              outlined
              label="Pago por convenio"
              :rules="regla_convenio"
              counter="6"
              type="number"
            />
            <v-text-field
              v-if="solicitud_recurso.metodo_pago === 'Referencia'"
              v-model="referencia"
              outlined
              :rules="regla_referencia"
              label="Pago por referencia"
              counter="16"
              type="number"
            />
            <v-text-field
              v-if="solicitud_recurso.metodo_pago == 'Linea de captura'"
              v-model="linea_captura"
              outlined
              :rules="regla_linea_captura"
              label="Liena de captura"
              counter="20"
              type="number"
            />
            <v-text-field
              v-if="isMetodoPagoGenereal"
              v-model="solicitud_recurso.datos_bancarios"
              :rules="[(v) => !!v || 'Es necesario escoger un tipo de pago general']"
              outlined
              disabled
            />
          </v-col>
          <v-col cols="12" sm="6">
            <v-card-title>Genereal</v-card-title>
            <v-divider></v-divider>
            <v-list
              :disabled="!isMetodoPagoGenereal || solicitud_recurso.beneficiario === null"
              :color="!isMetodoPagoGenereal ? 'grey' : ''">
              <v-list-item-group v-model="solicitud_recurso.datos_bancarios">
                <v-list-item
                  v-for="metodo_pago of metodos_pago" :key="metodo_pago.keys"
                  :value="(solicitud_recurso.beneficiario != null) ? solicitud_recurso.beneficiario[metodo_pago.keys]: ''"
                  @click="solicitud_recurso.metodo_pago = metodo_pago.texto"
                >
                  <template v-slot:default="{ active }">
                    <v-list-item-action>
                      <v-checkbox :input-value="active" color="primary" />
                    </v-list-item-action>
                    <v-list-item-content>
                      <v-list-item-title> <b>{{ (solicitud_recurso.beneficiario != null) ? solicitud_recurso.beneficiario[metodo_pago.keys] : "Seleccione un beneficiario"}}</b></v-list-item-title>
                      <v-list-item-subtitle>{{ metodo_pago.texto }}</v-list-item-subtitle>
                    </v-list-item-content>
                    <v-list-item-icon><v-icon>{{ metodo_pago.icono }}</v-icon></v-list-item-icon>
                  </template>
                </v-list-item>
              </v-list-item-group>
            </v-list>
          </v-col>
          <v-col cols="12" sm="6">
            <v-card-title>Otro</v-card-title>
            <v-divider></v-divider>
            <v-list
              :disabled="isMetodoPagoGenereal && solicitud_recurso.beneficiario !== null"
              :color="isMetodoPagoGenereal && solicitud_recurso.beneficiario !== null ? 'grey' : ''"
            >
              <v-list-item-group v-model="solicitud_recurso.metodo_pago">
                <v-list-item v-for="metodo_pago of otros_metodos_pago" :key="metodo_pago.pago"
                  :value="metodo_pago.texto"
                >
                  <template v-slot:default="{ active }">
                    <v-list-item-action>
                      <v-checkbox :input-value="active" color="primary" />
                    </v-list-item-action>
                    <v-list-item-content>
                      <v-list-item-title><b>Seleccionar para escribir</b></v-list-item-title>
                      <v-list-item-subtitle>{{ metodo_pago.texto }}</v-list-item-subtitle>
                    </v-list-item-content>
                    <v-list-item-icon><v-icon>{{ metodo_pago.icono }}</v-icon></v-list-item-icon>
                  </template>
                </v-list-item>
              </v-list-item-group>
            </v-list>
          </v-col>
        </v-row>
      </v-form>
    </v-card-text>
  </v-card>
</template>

<script>
import crearNuevoBeneficiario from '../../components/Beneficiario/CrearBeneficiario.vue'

export default {
  props: {},
  data: () => ({
    metodo_pago_seleccionado: null,
    otro_metodo_pago_seleccionado: null,
    solicitud_recurso:{
      'beneficiario': null,
      'datos_bancarios': null,
      'metodo_pago': ""
    },
    metodos_pago: [
      { texto: "Clabe", keys: "clave", icono: "mdi-key-variant" },
      { texto: "Cuenta", keys: "cuenta", icono: "mdi-account" },
      { texto: "Numero tarjeta", keys: "numero_tarjeta", icono: "mdi-credit-card" },
    ],
    otros_metodos_pago: [
      {texto: "Convenio", icono: "mdi-book-open"},
      {texto: "Referencia", icono: "mdi-account-switch"},
      {texto: "Linea de captura", icono: "mdi-barcode"}
    ],
    continuar_p5: false,
    regla_convenio: [
      v => !!v || 'Es necesario escribir un número de convenio',
      v => (v && v.length <= 6 && v.length >= 5 ) || 'Es necesario escbibir de 5 a 6 digitos'
    ],
    regla_referencia: [
      v => !!v || 'Es necesario escribir un número de referencia',
      v => (v && v.length === 16) || 'Es necesario escbibir de 16 digitos'
    ],
    regla_linea_captura: [
      v => !!v || 'Es necesario escribir un número de capturea',
      v => (v && v.length === 20) || 'Es necesario escbibir de 20 digitos'
    ]
  }),
  components:{
    'formulario-crear-beneficiario': crearNuevoBeneficiario,
  },
  computed: {
    linea_captura:{
      get(){ return this.solicitud_recurso.datos_bancarios },
      set(nuevoValor){
        if (this.$refs.formulario) 
          if(this.$refs.formulario.validate())
            this.solicitud_recurso.datos_bancarios = nuevoValor
      }
    },
    convenio:{
      get(){ return this.solicitud_recurso.datos_bancarios },
      set(nuevoValor){
        if (this.$refs.formulario) 
          if(this.$refs.formulario.validate())
            this.solicitud_recurso.datos_bancarios = nuevoValor
      }
    },
    referencia:{
      get(){ return this.solicitud_recurso.datos_bancarios },
      set(nuevoValor){
        if (this.$refs.formulario) 
          if(this.$refs.formulario.validate())
            this.solicitud_recurso.datos_bancarios = nuevoValor
      }
    },
    beneficiarios() { return this.$store.getters["beneficiario/getBeneficiarios"] },
    valid: {
      get(){ return this.continuar_p5 },
      set(){
        this.$emit("eventoContinuar", {'validacion': this.$refs.formulario})
        if (this.$refs.formulario) {
          if(this.$refs.formulario.validate()){
            this.$store.commit("MOD_pagos/assignSolicitudRecurso", this.solicitud_recurso)
          }
        }
      }
    },
    isMetodoPagoGenereal(){
      const { metodo_pago } = this.solicitud_recurso
      return metodo_pago !=='Convenio' && metodo_pago !=='Referencia' && metodo_pago !=='Linea de captura' ? true : false
    }
  },
  created(){
    this.inicializarBeneficiarios()
  },
  methods:{
    async inicializarBeneficiarios(){
      await this.$store.dispatch("beneficiario/obtenerBeneficiarios")
      // this.$store.commit("alerta/addAlertas", { "estado": estado, "mensaje": mensaje, "error": error })
    },
  }
};
</script>