<template>
  <v-data-table
    class="elevation-5"
    v-model="seleccionados"
    :headers="headers"
    :items="inmuebles"
    item-key="clave"
    :loading="isCargando"
    no-data-text="No hay inmuebles disponibles"
    loading-text="Cargando... por favor espere"
    :items-per-page="10"
    :footer-props="{
      itemsPerPageText: 'Filas por página',
      itemsPerPageAllText: 'Todos',
      pageText: 'p - Tp de Fp',
    }"
    :show-select="isMostrarSeleccion"
  >
    <template v-slot:top v-if="isMostrarMenuInmuebles">
      <v-toolbar flat>
        <v-toolbar-title>
          Inmuebles
          <div
            class="text-caption"
            v-text="inmuebles.length == 1 ? `${inmuebles.length} Inmueble` : `${inmuebles.length} Inmuebles`"
          />
        </v-toolbar-title>
        <v-spacer />
        <v-menu left offset-y>
          <template v-slot:activator="{ attrs, on }">
            <v-btn class="ma-5 info" v-bind="attrs" v-on="on">
              Añadir inmueble <v-icon right>mdi-menu-down</v-icon>
            </v-btn>
          </template>
          <v-list>
            <crear-inmueble/>
            <v-list-item link @click="$router.push({ name: 'cargar-archivo', params: cliente.name })">
              <v-list-item-title>
                Cargar archivo
              </v-list-item-title>
            </v-list-item>
          </v-list>
        </v-menu>
      </v-toolbar>
    </template>
  </v-data-table>
</template>

<script>
import FormularioCrearInmueble from  "../../components/Inmueble/FormularioCrearInmueblesManual.vue"
export default {
  props: {
    isMostrarSeleccion: { type: Boolean, default: false },
    isMostrarMenuInmuebles: { type: Boolean, default: true },
  },
  data: () => ({
    headers: [
      { value: "cliente", text: "Cliente" },
      { value: "clave", text: "Clave" },
      { value: "nombre", text: "Inmueble" },
      { value: "estado", text: "Estado" },
      { value: "municipio", text: "Municipio" },
      { value: "localidad", text: "Localidad" },
      { value: "direccion", text: "Direccion" },
    ],
    pageText: { pageStart: 1, pageStop: 1, itemsLength: 1 },
  }),
  components:{
    "crear-inmueble": FormularioCrearInmueble
  },
  computed: {
    inmuebles() { return this.$store.getters["MOD_inmuebles/getInmuebles"]; },
    isCargando() { return this.$store.getters["MOD_inmuebles/getCargando"]; },
    seleccionados: {
      get() { return []; },
      set(nuevoValor) {
        this.$store.commit(
          "MOD_inmuebles/setInmueblesSeleccionados",
          nuevoValor
        );
      },
    },
    cliente() { return this.$store.getters["MOD_clientes/getCliente"] }
  },
  
  created() {
    this.inicializarInmuebles()
  },

  methods:{
    async inicializarInmuebles(){
      const { mensaje, estado, error } = await this.$store.dispatch("MOD_inmuebles/listar", this.cliente.id)
      this.$store.commit("alerta/addAlertas", { "estado": estado, "mensaje": mensaje, "error": error })
    },
  }
};
</script>
