import axios from "@/api";

// import { localStorage2json, json2localStorage } from "@/utils/data/store";
// import { INITIALISER_FUNCTION, LOGOUT_FUNCTION } from '@/store/constants';


const state = {
  grupos: [],
  grupos_permisos: [],
  rolMiembro: {},
};

const mutations = {
  MUT_EstablecerRolMiembro(state, rol) {
    // json2localStorage("rolMiembro", rol)
    state.rolMiembro = rol
  },
  clearRolMiembro(state){
    // json2localStorage("rolMiembro", {})
    state.rolMiembro = {}
  }
};

const actions = {
  // [LOGOUT_FUNCTION]({ commit }) {
  //   commit('clearRolMiembro')
  // },
  // [INITIALISER_FUNCTION]({ commit }) {
  //   commit('MUT_EstablecerRolMiembro')
  // },
  async obtenerGrupos() {
    try {
      const { data } = await axios.instance.get("roles/grupos/");
      const { items } = data;
      return { items };
    } catch (error) {
      const strError = String(error.response.data.error);
      return { error: strError };
    }
  },
  async obtenerPermisos() {
    try {
      const { data } = await axios.instance.get("roles/permiso/");
      const { items } = data;
      return { items };
    } catch (error) {
      const strError = String(error.response.data.error);
      return { error: strError };
    }
  },
  async AXN_ActualizarRolMiembro(context, {usuario, rol}) {
    try {
      const { data } = await axios.instance.put(`roles/usuario/${usuario}/`, {'user': usuario, 'rol': rol});
      const { message } = data;
      return { message };
    } catch (error) {
      const strError = String(error.response.data.error);
      return { error: strError };
    }
  },
  async AXN_AsignarRolMiembro(context, {usuario, rol}) {
    try {
      const { data } = await axios.instance.post('roles/usuario/', {'user': usuario, 'rol': rol});
      const { message } = data;
      return { message };
    } catch (error) {
      const strError = String(error.response.data.error);
      return { error: strError };
    }
  },
  async AXN_ObtenerRolMiembro({ commit }, {id} ){
    try {
      const { data } = await axios.instance.get(
        `/roles/usuario/${id}/`,
      );
      const { items } = data
      commit("MUT_EstablecerRolMiembro", items.rol)
      return { message: "Ok" }
    } catch (error) {
      // const strMessage = String(error.response.data.message);
      return { message: "No tiene un rol.", error }
    }
  },
};

const getters = {
  GET_idRolMiembro: (state) => state.rolMiembro.id
};


export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters,
};
