import axios from '@/api'

import { localStorage2json, json2localStorage } from '@/utils/data/store';

import { INITIALISER_FUNCTION, LOGOUT_FUNCTION } from '@/store/constants';

const state = {
  token: null,
  usuario: [],
}

const actions = {

  [INITIALISER_FUNCTION]({ dispatch }) {
    dispatch('iniciarAutenticacion');
  },
  [LOGOUT_FUNCTION]({ commit }) {
    commit('limpiarTokens');
    commit('limpiarUsuario')
  },
  iniciarAutenticacion({ commit },) {
    const token = localStorage2json('token')
    const usuario = localStorage2json('usuario')
    commit('setTokens', token )
    commit('setUsuario', usuario )
  },

  async login({ commit }, { email, nombreUsuario, password }) {
    try {
      const { data, status } = await axios.instance.post('login/',
        { 'email': email, 'username': nombreUsuario, 'password': password }
      )
      const { token, user, message } = data
      const { groups, id, username, is_staff, name, last_name, image, is_active } = user
      commit('setTokens', token)
      commit('setUsuario', {
        'id': id,
        'grupos': groups,
        'usuario': username,
        'email': email,
        'nombre': name,
        'apellidos': last_name,
        'imagen': image,
        'is_active': is_active,
        'is_staff': is_staff,
      })
      return { 'mensaje': message, 'estado': status }
    } catch (error) {
      return { 'error': error }
    }
  },
  async cerrarSesion(context, token) {
    try {
      const { data } = await axios.instance.post(`logout/?token=${token}`)
      const { session_messege } = data
      return { 'message': session_messege }
    } catch (error) {
      const strError = String(error.response.data.error)
      return { 'error': strError, 'message': strError }
    }
  }
}

const mutations = {
  setTokens(state, token) {
    json2localStorage('token', token)
    state.token = token
  },
  setUsuario(state, usuario) {
    json2localStorage('usuario', usuario)
    state.usuario = usuario
  },
  limpiarTokens(state) {
    json2localStorage('token', null);
    //   json2localStorage('staff_refresh_token', null);
    state.token = null;
    //   state.staff_refresh_token = null;
  },
  limpiarUsuario(state) {
    json2localStorage('usuario', null)
    state.usuario = null
  }
}

const getters = {
  isUsuarioAutenticado: (state) => Boolean(state.token),
  getGrupos: (state) => state.usuario.grupos,
  getUserPermissions: (state) => state.usuario.permisoUsuario,
  getFotoUsuario: (state) => state.usuario.imagen,
  getNombreUsuario:(state) => state.usuario.usuario,
  obtenerNombreUsuario: (state) => state.usuario.nombre,
  obtenerApellidosUsuario: (state) => state.usuario.apellidos,
  obtenerIdUsuario:(state) => state.usuario.id,
  getToken:(state) => state.token
}

export default {
  namespaced: true,
  state,
  actions,
  mutations,
  getters,
}