<template>
  <v-dialog v-model="dialog" scrollable max-width="400">
    <template v-slot:activator="{ on, attrs }">
      <v-btn v-bind="attrs" v-on="on" color="primary" class="ma-1">
        <v-icon left>mdi-filter-variant</v-icon> Filtros
      </v-btn>
    </template>
    <v-card class="mx-auto">
      <v-card-title>
        <v-btn icon v-if="paso_filtro === 1" @click="dialog=false"><v-icon>mdi-chevron-left</v-icon></v-btn>
        <v-btn icon v-else @click="paso_filtro=1, filtro = -1"><v-icon>mdi-chevron-left</v-icon></v-btn>
        <v-spacer />
          {{ filtro > -1 ? opciones_filtrado[filtro].text : "" }}
        <v-spacer />
        <v-btn icon color="primary" @click="cleanAll()"><v-icon>mdi-eraser</v-icon></v-btn>
      </v-card-title>
      <v-divider />
        <v-stepper v-model="paso_filtro" elevation="0">
          <v-stepper-items>
            <v-stepper-content step="1">
              <v-list>
                <v-list-item-group v-model="filtro">
                  <v-list-item v-for="(filtro, i) in opciones_filtrado" :key="i"
                    @click="paso_filtro = i+2"
                  >
                    <template v-slot:default="{active}">
                      <v-list-item-content>
                        <v-list-item-title>{{ filtro.text }}</v-list-item-title>
                        <v-list-item-subtitle v-if="filtro.text==='Cliente'">{{ cliente ? cliente.nombre: "" }}</v-list-item-subtitle>
                        <v-list-item-subtitle v-if="filtro.text==='Estado'">{{ estado ? estado.nombre: "" }}</v-list-item-subtitle>
                        <v-list-item-subtitle v-if="filtro.text==='Fecha'">
                          {{ fecha_menor ? fecha_menor: "" }} {{ fecha_mayor ? fecha_mayor: "" }}
                        </v-list-item-subtitle>
                      </v-list-item-content>
                      <v-list-item-action>
                        <v-btn :input-value="active" icon><v-icon>mdi-chevron-right</v-icon></v-btn>
                      </v-list-item-action>
                    </template>
                  </v-list-item>
                </v-list-item-group>
              </v-list>
            </v-stepper-content>
            <v-stepper-content step="2">
              <v-select
                v-model="cliente"
                :items="clientes"
                label="Cliente"
                item-text="nombre"
                return-object
                class="mt-1"
                outlined
                dense
                clearable
              />
            </v-stepper-content>
            <v-stepper-content step="3">
              <v-select
                v-model="estado"
                :items="estados"
                label="Estado"
                item-text="nombre"
                return-object
                class="mt-1"
                outlined
                dense
                clearable
              />
            </v-stepper-content>
            <v-stepper-content step="4">
              <seleccionar-fecha
                @eventoFecha="setFechas"
              />
            </v-stepper-content>
          </v-stepper-items>
        </v-stepper>
        <v-card-actions>
          <v-btn block color="primary" @click="inicializarTrabajos()">Ver trabajos</v-btn>
        </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import SeleccionarFechas from "../../components/Seleccion/Date.vue";
export default {
  data: () => ({
    dialog: false,
    opciones_filtrado: [ {"text": "Cliente"}, {"text":"Estado"}, {"text": "Fecha"} ],
    paso_filtro: 1,
    filtro: -1,
    cliente: null,
    estado: null,
    fecha_mayor:null,
    fecha_menor:null
  }),
  components: {
    "seleccionar-fecha": SeleccionarFechas,
  },
  computed: {
    clientes() { return this.$store.getters["MOD_clientes/getClientes"]; },
    estados() { return this.$store.getters["MOD_ubicaciones/getEstados"]; },
  },
  created() {
    this.inicializarTrabajos()
  },
  methods: {
    setFechas({menorIgualFecha, mayorIgualFecha}){
      this.fecha_mayor = mayorIgualFecha
      this.fecha_menor = menorIgualFecha
      this.cliente = null
      this.estado = null
    },
    async inicializarTrabajos() {
      const { cliente, estado, fecha_menor, fecha_mayor } = this
      this.$store.commit("MOD_trabajos/cleanTrabajos")
      const filtado = {
        client: cliente ? cliente.id : null,
        province: estado ? estado.id : null,
        menorIgualFecha: cliente || estado ? null : fecha_menor,
        mayorIgualFecha: cliente || estado ? null : fecha_mayor,
      }
      const { mensaje, error, estados } = await this.$store.dispatch("MOD_trabajos/obtenerTrabajosPendietes", filtado);
      if (error) {
        await this.$store.commit("alerta/addAlertas", { "estado": estados, "mensaje": mensaje, 'error': error })
      }
    },
    async inicializarClientes() {
      await this.$store.dispatch("MOD_clientes/obtenerClientes");
    },
    async inicializarEstados() {
      await this.$store.dispatch("MOD_ubicaciones/obtenerEstados");
    },
    cleanAll(){
      this.cliente = null
      this.estado= null
      this.fecha_mayor=null
      this.fecha_menor=null
    }
  },
  watch: {
    paso_filtro(event){
      if(event === 2){ this.inicializarClientes() }
      if(event === 3){ this.inicializarEstados() }
    }
  },
};
</script>