<template>
  <v-row class="mt-1" justify="center">
    <v-col cols="auto">
      <v-menu
        ref="menu"
        v-model="menu"
        :close-on-content-click="false"
        :return-value.sync="menorIgualFechaSeleccionada"
        transition="scale-transition"
        offset-y
        min-width="auto"
      >
        <template v-slot:activator="{ on, attrs }">
          <v-text-field
            v-model="menorIgualFechaSeleccionada"
            label="Fecha de inicio"
            prepend-icon="mdi-calendar"
            readonly
            v-bind="attrs"
            v-on="on"
            clearable
            outlined
            dense
          />
        </template>
        <v-date-picker
          v-model="menorIgualFechaSeleccionada"
          no-title
          scrollable
          locale="es-es"
        />
      </v-menu>
    </v-col>
    <v-col cols="auto">
      <v-menu
        ref="menu2"
        v-model="menu2"
        :close-on-content-click="false"
        :return-value.sync="mayorIgualFechaSeleccionada"
        transition="scale-transition"
        offset-y
        min-width="auto"
      >
        <template v-slot:activator="{ on, attrs }">
          <v-text-field
            v-model="mayorIgualFechaSeleccionada"
            label="Fecha final"
            prepend-icon="mdi-calendar"
            readonly
            v-bind="attrs"
            v-on="on"
            clearable
            dense outlined
          />
        </template>
        <v-date-picker
          v-model="mayorIgualFechaSeleccionada"
          no-title
          scrollable
          locale="es-es"
        />
      </v-menu>
    </v-col>
  </v-row>
</template>

<script>
export default {
  data: () => ({
    menu: false,
    menu2: false,
    menorIgualFecha: new Date(
      Date.now() - new Date().getTimezoneOffset() * 60000
    ).toISOString().substr(0, 10),
    mayorIgualFecha: new Date(
      Date.now() - new Date().getTimezoneOffset() * 60000
    ).toISOString().substr(0, 10),
  }),
  computed:{
    menorIgualFechaSeleccionada:{
      get(){ return this.menorIgualFecha },
      set(nuevoValor){
        this.$refs.menu.save(nuevoValor);
        this.menorIgualFecha = nuevoValor
        this.$emit("eventoFecha", {
          menorIgualFecha: nuevoValor,
          mayorIgualFecha: this.mayorIgualFecha,
        });
      }
    },
    mayorIgualFechaSeleccionada:{
      get() { return this.mayorIgualFecha },
      set(nuevoValor){
        this.$refs.menu2.save(nuevoValor);
        this.mayorIgualFecha = nuevoValor
        this.$emit("eventoFecha", {
          menorIgualFecha: this.menorIgualFecha,
          mayorIgualFecha: nuevoValor,
        });
      }
    }
  }
};
</script>
