<template>
  <v-card class="mx-auto">
    <v-card-title dense elevation="1">
      Cliente
      <v-spacer />
      <nuevo-cliente />
    </v-card-title>
    <v-divider></v-divider>
    <listar-clientes />
    <v-row>
      <v-col class="text-subtitle-1 text-center" cols="12" v-if="isCargando">
        Obteniendo los clientes
      </v-col>
      <v-col
        class="text-subtitle-1 text-center"
        cols="12"
        v-if="!isCargando && !isCliente"
      >
        No hay clientes
      </v-col>
      <v-col cols="6">
        <v-progress-linear
          :active="isCargando"
          :indeterminate="isCargando"
          absolute
          bottom
          color="deep-purple accent-4"
        ></v-progress-linear>
      </v-col>
    </v-row>
  </v-card>
</template>

<script>
import CrearCliente from "../../components/Cliente/FormularioCrearCliente.vue";
import ListarClientes from "../../components/Cliente/MostrarClientes.vue";
export default {
  components: {
    "nuevo-cliente": CrearCliente,
    "listar-clientes": ListarClientes,
  },
  computed: {
    isCargando() {
      return this.$store.getters["MOD_clientes/getCargando"];
    },
    isCliente() {
      return this.$store.getters["MOD_clientes/isClientes"];
    },
  },
};
</script>
