<template>
  <v-card class="mx-auto" outlined elevation="5">
    <v-card-title>
      Inmuebles seleccionados: {{ inmueblesSeleccionados.length }}/{{ inmuebles.length }}
    </v-card-title>
    <v-card-actions>
      <v-spacer></v-spacer>
      <listar-territorios-de-inmuebles-existentes />
    </v-card-actions>
    <v-card-text>
      <tabla-inmuebles
        :isMostrarSeleccion="true"
        :isMostrarMenuInmuebles="false"
      />
    </v-card-text>
  </v-card>
</template>
<script>
import MostrarInmuebles from "../components/Inmueble/MostrarInmuebles.vue";
import FormularioPOSTTerritoriosInmuebles from "./Inmueble/FormularioPOSTTerritoriosInmuebles.vue";

export default {
  components: {
    "tabla-inmuebles": MostrarInmuebles,
    "listar-territorios-de-inmuebles-existentes": FormularioPOSTTerritoriosInmuebles,
  },
  computed: {
    inmuebles() {
      return this.$store.getters["MOD_inmuebles/getInmuebles"];
    },
    inmueblesSeleccionados(){
      return this.$store.getters['MOD_inmuebles/getInmueblesSeleccionados']
    },
  },
};
</script>
