<template>
  <v-toolbar flat>
    <v-dialog v-model="dialog" persistent :retain-focus="false" max-width="700">
      <v-card>
        <v-card-title>
          Asignar rol
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-autocomplete
              v-model="rol"
              :items="grupos_permisos"
              item-text="name"
              item-value="id"
              label="Grupos"
              required
            />
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer />
          <v-btn
            v-if="!rolUsuarioEncontrado"
            @click="asignarRolMiembro()"
            color="green"
            class="ma-5"
            >Asignar rol</v-btn
          >
          <v-btn
            v-if="rolUsuarioEncontrado"
            @click="actualizarRolMiembro()"
            color="warning"
            class="ma-5"
            >Actualizar rol</v-btn
          >
          <v-btn @click="cerrar()" color="red" class="ma-5">Cerrar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-toolbar>
</template>

<script>
export default {
  props: {
    dialog: { type: Boolean, default: false },
    groups: { type: Number, default: null },
    idMiembro: { type: Number, default: null },
    rolUsuarioEncontrado: { type: Boolean, default: false },
  },
  data: () => ({
    rol: null,
    grupos_permisos: [],
  }),
  created() {
    this.listarGrupos();
  },
  methods: {
    async listarGrupos() {
      const { items, error } = await this.$store.dispatch(
        "MOD_grupos/obtenerGrupos"
      );
      if (error) {
        alert(error);
        return 0;
      }
      this.grupos_permisos = items;
    },
    async actualizarRolMiembro() {
      const { rol, idMiembro } = this;
      const {
        message,
        error,
      } = await this.$store.dispatch("MOD_grupos/AXN_ActualizarRolMiembro", {
        usuario: idMiembro,
        rol: rol,
      });
      if (error) {
        alert(message);
      }
      alert(message);
    },
    async asignarRolMiembro() {
      const { rol, idMiembro } = this;
      const {
        message,
        error,
      } = await this.$store.dispatch("MOD_grupos/AXN_AsignarRolMiembro", {
        usuario: idMiembro,
        rol: rol,
      });
      if (error) {
        alert(message);
      }
      alert(message);
    },
    cerrar() {
      this.$emit("establecerDialog", false);
    },
  },
  watch: {
    groups(nuevoValor) {
      this.rol = nuevoValor;
    },
  },
};
</script>
