<template>
  <v-main>
    <v-card class="mx-auto">
      <v-breadcrumbs :items="items">
        <template v-slot:item="{ item }">
          <v-breadcrumbs-item :href="item.href" :disabled="item.disabled">
            {{ item.text.toUpperCase() }}
          </v-breadcrumbs-item>
        </template>
      </v-breadcrumbs>
      <v-tabs>
        <v-tab :to="{name: 'inmuebles'}">Inmuebles</v-tab>
        <v-tab :to="{name: 'trabajos-inmuebles'}">Trabajos</v-tab>
        <v-tab disabled>Recursos</v-tab>
        <v-tab disabled>Configuraciones</v-tab>
      </v-tabs>
    </v-card>
    <br />
    <router-view />
  </v-main>
</template>

<script>
export default {
  data: () => ({
    items: [],
  }),
  computed: {
    cliente() { return this.$store.getters["MOD_clientes/getCliente"]; },
    nombreUsuario() { return this.$store.getters["MOD_autenticacion/getNombreUsuario"]; },
  },
  created() {
    this.agregarMigas({ text: this.nombreUsuario, disable: false, href: "#" });
    this.agregarMigas({ text: this.cliente.nombre, disable: false, href: "#" });
  },
  methods: {
    agregarMigas(nuevoValor) {
      this.items.push(nuevoValor);
    },
  },
};
</script>
