import axios from '@/api'

import { PDFBase64ToBlob, generateRandomFileName } from '@/utils/helpers/PDFUploading';

// import { localStorage2json, json2localStorage } from '@/utils/data/store';

// import { INITIALISER_FUNCTION, LOGOUT_FUNCTION } from '@/store/constants';


const state = {
  trabajo: {},
  trabajos: [],

  cargando: false,
  
  asignaciones: [],
  asignacionesVerificadas: [],

  seleccionados: [],

  status_trabajo: [],
  estado_trabajo: {}
}

const mutations = {
  setTrabajo(state, resultado) {
    // json2localStorage('trabajo', resultado)
    state.trabajo = resultado
  },
  setTrabajos(state, payload) {
    // json2localStorage('trabajos', resultado)
    state.trabajos = payload
  },
  limpiarTrabajo(state) {
    // json2localStorage('trabajo', null)
    state.trabajo = null
  },
  cleanTrabajos(state) {
    // json2localStorage('trabajos', null)
    state.trabajos = []
  },
  setCargando(state, payload) {state.cargando = payload },
  setStatusTrabajo(state, payload) { state.status_trabajo = payload },
  setEstadoTrabajo(state, payload) { state.estado_trabajo = payload },
  addAsignaciones(state, payload) { state.asignaciones.push(payload) },
  setAsignacionesVerificadas(state, payload) { state.asignacionesVerificadas = payload },
  addAsignacionesVerificadas(state, {index, payload}) { state.asignacionesVerificadas[index].trabajos = state.asignacionesVerificadas[index].trabajos.concat(payload.trabajos)},
  cleanAsignacionesVerificadas(state){ state.asignacionesVerificadas = [] },
  setSeleccionados(state, payload) { state.seleccionados = payload },
  deleteSeleccionado(state, editarIndice){ state.seleccionados.splice(editarIndice, 1) },
  cleanSeleccionados(state) { state.seleccionados = [] }
}

const actions = {
  // [INITIALISER_FUNCTION]({ dispatch }) {
  //   dispatch('iniciarTrabajos')
  // },
  // [LOGOUT_FUNCTION]({ commit }) {
  //   commit('limpiarTrabajo')
  //   commit('limpiarTrabajos')
  // },
  // iniciarTrabajos({ commit },) {
  //   const trabajo = localStorage2json('trabajo')
  //   commit('setTrabajo', trabajo)
  // },

  async obtenerStatusTrabajos({ commit }) {
    try {
      commit('setCargando', true)
      const { data, status } = await axios.instance.get('/trabajos/status/')
      const { items, message } = data
      const estados_trabajo = []
      for(const estado_trabajo of items){
        estados_trabajo.push({
          'id': estado_trabajo.id,
          'nombre': estado_trabajo.name
        })
      }
      commit('setStatusTrabajo', estados_trabajo)
      commit('setCargando', false)
      return {'mensaje': message, 'estado': status }
    } catch (error) {
      commit('setCargando', false)
      return { 'error': error }
    }
  },
  async crearStatusTrabajo({commit}, {nombre, proyecto_id}){
    try {
      commit('setCargando', true)
      const estado = { 'name': nombre, 'project': proyecto_id }
      const { data, status } = axios.instance.post('/trabajos/status/', estado)
      const { items, message } = data
      const nuevo_estado = { 'id': items.id,  'nombre': items.name }
      commit('setEstadoTrabajo', nuevo_estado)
      commit('setCargando', false)
      return { 'mensaje': message, 'estado':status }
    } catch (error) {
      commit('setCargando', false)
      return {'error': error}
    }
  },
  async obtenerTrabajosPendietes({ commit }, { client, province, menorIgualFecha, mayorIgualFecha }) {
    try {
      commit('setCargando', true)
      var url_trabajo = 'trabajos/pendiente/'
      if (client || province){
        url_trabajo = url_trabajo + `?client=${client ? client : ''}&province=${province ? province : ''}`
      }
      if (menorIgualFecha || mayorIgualFecha){
        url_trabajo = url_trabajo + `?date_gte=${menorIgualFecha ? menorIgualFecha : ''}&date_lte=${mayorIgualFecha ? mayorIgualFecha : ''}`
      }
      const { data, status } = await axios.instance.get(url_trabajo)
      const { items, message } = data
      commit('setTrabajos', items)
      commit('setCargando', false)
      return { 'mensaje': message, 'estado' : status}
    } catch (error) {
      commit('cleanTrabajos')
      commit('setCargando', false)
      return { 'error': error }
    }
  },

  async obtenerTrabajosCliente({ commit }, { cliente }){
    try {
      commit('setCargando', true)
      const { data, status } = await axios.instance.get(`trabajos/cliente/?cliente=${cliente}`)
      const { items, message } = data
      commit('setTrabajos', items)
      commit('setCargando', false)
      return { 'mensaje': message, 'estado' : status}
    } catch (error) {
      commit('cleanTrabajos')
      commit('setCargando', false)
      return { 'error': error }
    }
  },

  async recuperarTrabajo({ commit }, id) {
    try {
      const { data } = await axios.instance.get(`trabajos/trabajo/${id}/`)
      const { items } = data
      commit('setTrabajo', items)
      return
    } catch (error) {
      alert('Error.')
      return
    }
  },

  async AXN_SubirArchivo(context, { work, PDFBase64 }) {
    const title = generateRandomFileName();
    const blobPDF = await PDFBase64ToBlob(PDFBase64);
    const form = new FormData()
    form.append('work', work)
    form.append('title', title)
    if (blobPDF) {
      form.append('file', PDFBase64, `${title}.pdf`);
    }
    try {
      await axios.instance.post('/trabajos/archivo/', form)
      return { 'message': 'Archivo subido.' }
    } catch (error) {
      const mensajeError = String(error.response.data.error)
      return { 'message': 'No se pudo subir el archivo.', 'error': mensajeError }
    }
  },

  async AXN_ActualizaArchivo(context, { id, work, title, PDFBase64 }) {
    const blobPDF = await PDFBase64ToBlob(PDFBase64);
    const form = new FormData()
    form.append('work', work)
    form.append('title', title)
    if (blobPDF) {
      form.append('file', PDFBase64, `${title}.pdf`);
    }

    try {
      await axios.instance.put(`/trabajos/archivo/${id}/`, form)
      return { 'message': 'Archivo actualizado.' }
    } catch (error) {
      const mensajeError = String(error.response.data.error)
      return { 'message': 'No se pudo actualizar el archivo.', 'error': mensajeError }
    }
  },

  async updatework({ commit }, { id, status_id, project_id, concept_id, property_office_id, assigned_user_id, area_user_id, send_review }) {
    try {
      const { data } = await axios.instance.put(`/trabajos/trabajo/${id}/`,
        { status_id, project_id, concept_id, property_office_id, assigned_user_id, area_user_id, send_review }
      )
      commit('setTrabajo', data)
      return { 'message': 'Enviado para revisar' }
    } catch (error) {
      const mensajeError = String(error.response.data.error)
      return { 'message': 'No se pudo enviar el trabajo', 'error': mensajeError }
    }
  },

  async AXN_ComentarTrabajo(context, { id_trabajo, comentario }) {
    try {
      const { data } = await axios.instance.post('/trabajos/comentario/', {
        'work': id_trabajo,
        'comment': comentario
      })
      const { message } = data
      return { message }
    } catch (error) {
      const mensajeError = String(error.response.data.error)
      return { 'message': mensajeError }
    }
  },

  async obtenerTrabajosUsuario({ commit }, {username}) {
    try {
      commit('setCargando', true)
      const { data, status } = await axios.instance.get(`trabajos/usuario/?username=${username}`,)
      const { items, message } = data;
      const trabajos = []
      items.forEach(element => {
        // if (element.status == status){
          trabajos.push({
            id: element.id,
            proyecto: element.concept.project,
            concepto: element.concept.name,
            status: element.status,
            inmueble: {
              nombre: element.property_office.name,
              clave: element.property_office.property_key,
              estado: element.property_office.province,
              municipio: element.property_office.municipality,
            },
            enviado: element.send_review,
            crado: element.created_date,
            modificado: element.modified_date
          })
        // }
      });
      commit('setTrabajos', trabajos)
      commit('setCargando', false)
      return { 'mensaje': message, 'estado': status }
    } catch (error) {
      commit('setCargando', false)
      return { 'error': error };
    }
  },

  async verificarAsignaciones({ commit, dispatch, getters }, asignaciones) {
    try {
      const assignments = await dispatch('serializarAsignacion', asignaciones)
      const { data, status } = await axios.instance.post("trabajos/trabajo/verificar_asignacion/", {
        'assignments': assignments,
      })
      const { items, message } = data
      const asignacionesVerificadas = []
      items.forEach((element, index) => {
        asignacionesVerificadas.push({
          'inmueble': element.property_office,
          'usuario_area': element.area_user,
          'trabajos': []
        })
        for(const trabajo of element.works){
          asignacionesVerificadas[index].trabajos.push({
            'concepto': trabajo.concept,
            'usuario_asignado': trabajo.assigned_user,
            'estado': trabajo.status,
            'codido_estado': trabajo.status_code,
            'mensaje': trabajo.message ? trabajo.message : trabajo.warning,
            'confirmacion': trabajo.confirmation
          })
        }
      });
      if(getters["getAsignacionesVerificadas"].length){
        await dispatch("compararAsignaciones", asignacionesVerificadas)
      } else {
        commit("setAsignacionesVerificadas", asignacionesVerificadas);
      }
      return { 'mensaje': message, 'estado': status }
    } catch (error) {
      return { 'error': error }
    }
  },

  async confirmarAsignacion({dispatch}, asignaciones) {
    const assignments = await dispatch('serializarAsignacion', asignaciones)
    try {
      const {data, status} = await axios.instance.post("trabajos/trabajo/confirmar_asignacion/", {
        'assignments': assignments
      })
      const {message}=data
      // commit("setAsignaciones", items)
      return { 'mensaje': message, 'estado': status }
    } catch (error) {
      return {'error': error}
    }
  },
  async serializarAsignacion(context, asignaciones){
    const asignacionesVerificadas = []
    await asignaciones.forEach((element, index) => {
      asignacionesVerificadas.push({
        'property_office': element.inmueble,
        'area_user': element.usuario_area,
        'works': []
      })
      for(const trabajo of element.trabajos){
        asignacionesVerificadas[index].works.push({
          'concept': trabajo.concepto,
          'assigned_user': trabajo.usuario_asignado,
          'status': trabajo.estado,
          'message': trabajo.mensaje,
          'confirmation': trabajo.confirmacion
        })
      }
    })
    return asignacionesVerificadas
  },
  async compararAsignaciones({getters, commit}, asignacionesVerificadas){
    const asignaciones = getters["getAsignacionesVerificadas"]
    for(let asignacionVerificada of asignacionesVerificadas){
      const asignacion_obj = await asignaciones.find((item => item.inmueble === asignacionVerificada.inmueble))
      if(asignacion_obj){
        const index = asignaciones.indexOf(asignacion_obj)
        commit("addAsignacionesVerificadas",{index: index, payload: asignacionVerificada})
      }
    }
  }
}

const getters = {
  getStatusTrabajo: (state) => state.status_trabajo,
  getEstadoTrabajo: (state) => state.estado_trabajo,
  getTrabajo: (state) => state.trabajo,
  getTrabajos: (state) => state.trabajos,
  getArchivosTrabajo: (state) => state.trabajo.files,
  getCargando: (state) => state.cargando,
  getAsignaciones: (state) => state.asignaciones,
  getAsignacionesVerificadas: (state) => state.asignacionesVerificadas,
  getTrabajosSeleccionados: (state) => state.seleccionados
}

export default {
  namespaced: true,
  state,
  actions,
  mutations,
  getters
}