<template>
  <v-row dense justify="center">
    <v-col cols="12" sm="8">
      <recursos-usuario :addHeaders="headers" />
    </v-col>
  </v-row>
</template>

<script>
import ListarRecursosUsuario from '../../components/Pago/ValidarPeticiones.vue'
export default {
  data:() =>({
    headers: [{text: "Solicitado", value: "solicitud", align: 'center'}, {text: "Validado", value: "validado", align: 'center'}]
  }),
  components:{
    'recursos-usuario': ListarRecursosUsuario
  },
  computed: {
    username() { return this.$store.getters["MOD_autenticacion/obtenerIdUsuario"]; },
  },
  created() {
    this.inicializarRecursosUsuario()
  },
  methods:{
    async inicializarRecursosUsuario(){
      const { mensaje, estado, error } = await this.$store.dispatch("MOD_pagos/recuperarRecursosUsuario", { solicitado: this.username });
      if (error) {
        this.$store.commit("alerta/addAlertas", { estado: estado, mensaje: mensaje, error: error, });
      }
    }
  }
}
</script>