<template>
  <v-card-actions>
    <v-btn
      v-for="[link, routerName] in links"
      :key="link"
      @click="redireccionar(routerName)"
      text>
      {{ link }}
    </v-btn>
    
  </v-card-actions>
</template>

<script>
export default {
  data: () => ({
    links: [
      ["Trabajos", "TrabajoUsuario"],
      ["Recursos", "recursos-usuario"],
      ["Pagos", "pagos"],
    ],
  }),
  computed: {
    usernname() {
      return this.$store.getters["MOD_autenticacion/getNombreUsuario"];
    },
  },
  methods: {
    redireccionar(routerName) {
      this.$router.push({
        name: routerName,
        params: { nombreUsuario: this.usernname },
      });
    },
  },
};
</script>
