<template>
  <v-row justify="center">
    <v-col cols="12" sm="2">
      <v-sheet color="grey lighten-4" class="pa-4">
        <h1><b>Pagos</b></h1>
      </v-sheet>
      <v-list>
        <v-list-item-group v-model="pago_seleccionado">
          <v-list-item v-for="[text] in tipo_pago" :key="text" link>
            <v-list-item-content >
              <v-list-item-title>{{ text }}</v-list-item-title>
            </v-list-item-content>
            <v-list-item-action>{{recursos_n.length}}</v-list-item-action>
          </v-list-item>
        </v-list-item-group>
      </v-list>
    </v-col>
    <v-col cols="12" sm="6">
      <peticiones :isValidarRecurso="isValidarRecurso" />
    </v-col>
  </v-row>
</template>

<script>
import Peticiones from "../../components/Pago/ValidarPeticiones.vue";

export default {
  data: () => ({
    tipo_pago: [
      ["Transferencia"],
      ["Cheque"],
      ["Reembolso"],
      ["Comprobacion"],
    ],
    pago: 0,
    isValidarRecurso: true
  }),
  components: {
    peticiones: Peticiones
  },
  computed:{
    recursos_n() { return this.$store.getters["MOD_pagos/getRecursos"]; },
    pago_seleccionado:{
      get(){ return this.pago },
      set(nuevoValor){
        const pago = nuevoValor === undefined ? this.pago : nuevoValor
        this.pago = pago
        if (this.pago === 0) { this.inicializarRecursos({ pago: "Transferencia", bool: "False"})
          this.isValidarRecurso =true
        }
        else if (this.pago === 1){ this.inicializarRecursos({pago: "Cheque", bool:"False"})
          this.isValidarRecurso =true
        }
        else if (this.pago === 2){ this.inicializarRecursos({pago: "Cheque", bool: "True"}), this.isValidarRecurso =false }
        else { this.inicializarRecursos({pago: "Transferencia", bool: "True"}), this.isValidarRecurso =false }
      }
    }
  },
  created(){
    this.inicializarRecursos({ pago: "Transferencia", bool: "False"});
  },
  methods: {
    async inicializarRecursos({pago, bool}) {
      const { mensaje, estado, error } = await this.$store.dispatch("MOD_pagos/recuperarRecursos", {
        tipo_pago: pago, validado: bool
      });
      if (error) {
        this.$store.commit("alerta/addAlertas", { estado: estado, mensaje: mensaje, error: error, });
      }
    },
  }
};
</script>