<template>
  <v-list dense>
    <v-list-item-group rounded>
      <v-list-item v-for="(cliente, i) in clientes" :key="i" link>
        <v-list-item-avatar @click="irInmuebles(cliente)">
          <v-avatar color="grey">
            <span class="white--text">{{ cliente.nombre[0].toUpperCase() }}</span>
          </v-avatar>
        </v-list-item-avatar>
        <v-list-item-content @click="irInmuebles(cliente)">
          <v-list-item-title v-text="cliente.nombre" />
        </v-list-item-content>
      </v-list-item>
    </v-list-item-group>
  </v-list>
</template>

<script>
export default {
  computed: {
    clientes() { return this.$store.getters["MOD_clientes/getClientes"]; },
  },
  created() {
    this.inicializarClientes();
  },
  methods: {
    async inicializarClientes() {
      const { error, mensaje, estado } = await this.$store.dispatch(
        "MOD_clientes/obtenerClientes"
      );
      if(error){
        this.$store.commit("alerta/addAlertas", {
          estado: estado,
          mensaje: mensaje,
          error: error,
        });
      }
    },
    irInmuebles(cliente) {
      this.$store.dispatch("MOD_clientes/recuperarCliente", cliente);
      this.$router.push({
        name: "inmuebles",
        params: { cliente: cliente.nombre },
      });
    },
  },
};
</script>
