<template>
  <v-data-table
    :headers="headers"
    :items="inmuebles"
    class="elevation-5"
    :footer-props="{
      itemsPerPageText: 'Filas por página',
      itemsPerPageAllText: 'Todos',
      pageText: ' p - Tp de n',
    }"
  >
    <template v-slot:item.status="{item}">
      <!-- <v-icon v-if="!item.status">mdi-checkbox-blank-circle-outline</v-icon> -->
      <v-icon :color="isValido(item) ? 'success': 'red'">
        {{isValido(item) ? 'mdi-checkbox-marked-circle-outline': 'mdi-close-circle-outline'}}
      </v-icon>
    </template>
    <template v-slot:item.estado="{item}">
      {{ typeof item.estado === 'object' ? item.estado.nombre : item.estado}}
    </template>
    <template v-slot:item.municipio="{item}">
      {{ typeof item.municipio === 'object' ? item.municipio.nombre : item.municipio}}
    </template>
    <template v-slot:item.localidad="{item}">
      {{ typeof item.localidad === 'object' ? item.localidad.nombre : item.localidad}}
    </template>
    <!-- <template v-slot:item.accion="{item}">
      <v-icon color="info" @click="habilitarEdicion(item)" small>mdi-pencil</v-icon>
      <v-icon color="red" @click="eliminarInmuebleAgregado(item)" small>mdi-delete</v-icon>
    </template> -->
  </v-data-table>
</template>

<script>

export default{
  data: () => ({
    headers: [   
      { value: 'status', text: 'Status', sortable: true },   
      { value: 'clave', text: 'Clave'},
      { value: "sirh", text: "SIRH" },
      { value: 'nombre', text: 'Inmueble'},
      { value: 'estado', text: 'Estado'},
      { value: 'municipio', text: 'Municipio'},
      { value: 'localidad', text: 'Localidad'},
      { value: 'direccion', text: 'Direccion'},      
      { value: 'accion', text: ''},
    ], 
  }),
  computed:{
    inmuebles(){ return this.$store.getters["MOD_inmuebles/getInmueblesVerificados"] },
  },
  methods:{
    eliminarInmuebleAgregado(inmueble){
      this.$store.commit("MOD_inmuebles/eliminarInmueble", inmueble)
    },
    habilitarEdicion(index){
      this.$store.commit("MOD_inmuebles/setInmueble", index)
      index.estado.id ? this.obtenerMunicipiosDeEstado(index.estado) : []
      this.dialog = true
    },
    async obtenerMunicipiosDeEstado({ id }) {
      await this.$store.dispatch("MOD_ubicaciones/obtenerMunicipiosDeEstado", id);
    },
    isValido({nombre, estado, municipio, cliente, clave}){
      if(nombre === 'n/a') { return false }
      if(JSON.stringify(clave) === '{}' || clave === 'n/a') { return false }
      if(JSON.stringify(estado) === '{}' || estado === 'n/a') { return false }
      if(JSON.stringify(municipio) === '{}' || municipio === 'n/a' ){return false}
      if(JSON.stringify(cliente) === '{}' || cliente === 'n/a' ){ return false }
      return true
    }
  }
}
</script>
