<template>
  <v-container class="grey lighten-2">
    <v-row no-gutters class="mb-6">
      <v-col cols="12">
        <v-timeline align-top dense>
          <v-timeline-item
            v-for="comment in trabajo.comments"
            :key="comment.time"
            small
          >
            <div>
              <div class="font-weight-normal">
                <strong>{{ comment }}</strong>
              </div>
            </div>
          </v-timeline-item>
        </v-timeline>
      </v-col>
    </v-row>
    <v-row no-gutters>
      <v-col cols="12">
        <v-card class="elevation-3">
          <v-card-text>
            <v-textarea
              v-model="comment"
              solo
              name="input-7-4"
              label="Escribir comentario"
            />
          </v-card-text>

          <v-card-actions>
            <v-spacer />
            <v-btn>
              <v-icon left color="red">mdi-source-commit-end</v-icon>
              Terminar trabajo
            </v-btn>
            <v-btn
              color="success"
              :disabled="comment.length == 0 ? true : false"
              @click="subirComentario()"
            >
              Comentar
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
  data: () => ({
    comment: "",
  }),
  computed: {
    trabajo() {
      return this.$store.getters["MOD_trabajos/getTrabajo"];
    },
  },
  methods: {
    async subirComentario() {
      const { trabajo, comment } = this;
      const { message } = await this.$store.dispatch(
        "MOD_trabajos/AXN_ComentarTrabajo",
        {
          id_trabajo: trabajo.id,
          comentario: comment,
        }
      );
      alert(message);
    },
  },
};
</script>
