<template>
  <v-dialog transition="dialog-top-transition" max-width="500" persistent>
    <template v-slot:activator="{ on, attrs }">
      <v-btn color="primary" v-bind="attrs" v-on="on" icon fab>
        <v-icon>mdi-plus</v-icon>
      </v-btn>
    </template>
    <template v-slot:default="dialog">
      <v-card class="mx-auto" elevation="5">
        <v-card-title class="grey lighten-2">
            Crear nuevo beneficiario
            <v-spacer />
            <v-btn @click="cerrrarDialogo(dialog)" color="error" icon>
              <v-icon>mdi-close</v-icon></v-btn>
          </v-card-title>
        <v-card-text>
          <v-form v-model="valid" ref="formularioNuevoBeneficiario" lazy-validation>
            <v-row dense>
              <v-col cols="12">
                <v-text-field v-model="beneficiario.nombre" label="Beneficiario" :rules="[(v) => !!v || 'Es necesario escribir un nombre del para el beneficiario']"></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field v-model="beneficiario.clave" label="Clave" :rules="[(v) => !!v || 'Es necesario escribir la clave']" type="number"></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field v-model="beneficiario.numero_tarjeta" label="Numero tarjeta" :rules="[(v) => !!v || 'Es necesario escribir el número de la tarjeta']" type="number"></v-text-field>
              </v-col>
            </v-row>
          </v-form>
        </v-card-text>
        <v-card-actions>
            <v-spacer />
            <v-btn color="success" :disabled="!valid" outlined @click="crearBeneficiario(dialog)">Crear</v-btn>
            <v-btn color="error" @click="cerrrarDialogo(dialog)" outlined>Cancelar</v-btn>
        </v-card-actions>
      </v-card>
    </template>
  </v-dialog>
</template>

<script>
export default {
  data:() => ({
    valid: true,
    beneficiario: {
      nombre: '',
      clave: null,
      numero_tarjeta: null
    }
  }),
  methods:{
    cerrrarDialogo(dialog){
      dialog.value = false
      this.$refs.formularioNuevoBeneficiario.reset()
    },
    async crearBeneficiario(dialog){
      if(this.$refs.formularioNuevoBeneficiario.validate()){
        const { mensaje, estado, error } =  await this.$store.dispatch("beneficiario/crearBeneficiario", this.beneficiario)
        this.$store.commit("alerta/addAlertas", { "estado": estado, "mensaje": mensaje, "error": error })
        if(!error){ this.cerrrarDialogo(dialog) }
      }
    }
  }
}
</script>