    <v-card-text>
      <v-row>
        <v-col cols="12" sm="8">
          <v-row>
            <v-col cols="12" sm="12">
              <v-data-table
                :headers="headers"
                :items="recurso.peticiones"
                loading-text="Cargando... por favor espere"
                :footer-props="{
                  itemsPerPageText: 'Filas por página',
                  pageText: '',
                }"
                no-data-text="No tienes trabajos."
                class="elevation-5 row-pointer"
                :loading="cargando"
              >
                <template v-slot:item.importe="{item}">
                  $ {{ item.importe.toFixed(2)}}
                </template>
              </v-data-table>
            </v-col>
            <v-col cols="12" sm="4">
              <v-text-field v-model="nombre" dense solo label="Nombre del archivo" />
            </v-col>
            <v-col cols="12" sm="4">
              <v-file-input
                label="PDF"
                dense
                outlined
                v-model="PDFBase64"
                accept=".pdf"
                show-size
                :append-outer-icon="PDFBase64 ? 'mdi-cloud-upload' : 'mdi-send-off'"
                @click:append-outer="subirArchivo"
                :disabled="cargando"
                :loading="cargando"
                clearable
              ></v-file-input>
            </v-col>
            <v-col cols="12" sm="4">
              <v-file-input
                v-model="XMLFile"
                label="XML"
                dense
                accept=".xml"
                show-size
                outlined
                :append-outer-icon="XMLFile ? 'mdi-cloud-upload' : 'mdi-send-off'"
                :disabled="cargando"
                :loading="cargando"
                clearable
                @click:append-outer="parseXml"
              ></v-file-input>
            </v-col>
            <v-col cols="12" sm="12">
              <v-card class="mx-auto">
                <v-list>
                  <v-subheader>{{archivos.length ? 'Comprobantes' : 'No se han subido comprobantes.'}}</v-subheader>
                    <v-list-item v-for="(archivo, index) in archivos" :key="index">
                      <v-list-item-content>
                        <a :href="archivo.archivo">{{archivo.nombre}}</a>
                      </v-list-item-content>
                      <v-list-item-action>
                        <confirmar
                          mensaje="Estás a punto de eliminar esta archivo. ¿Estás seguro?"
                          :id="archivo.id"
                          :indice="index"
                          :tipo_pago="('¿Eliminar ' + archivo.nombre +'?')"
                          @emitConfirmacion="eliminarArchivoRecurso"
                          icon="mdi-delete"
                          color="error"
                        />
                      </v-list-item-action>
                    </v-list-item>
                </v-list>
              </v-card>
            </v-col>
          </v-row>
        </v-col>
        <v-divider vertical class="black"></v-divider>
        <v-col cols="12" sm="4">
          <Comentarios />
        </v-col>
      </v-row>
    </v-card-text>
