<template>
  <v-row>
    <v-col cols="12" md="12" lg="12" sm="12">
      <v-btn title outlined><v-icon left>mdi-chevron-left</v-icon>Volver</v-btn>
    </v-col>
    <v-col cols="12" md="6" lg="6" sm="6">
      <v-card class="mx-auto" elevation="12" color="primary">
        <v-card-title>
          Estado del trabajo
        </v-card-title>
        <v-card-text>
          <v-list dense class="transparent">
            <v-list-item>
              <v-list-item-content>Estado del trabajo:</v-list-item-content>
              <v-list-item-content class="align-end">
                {{ trabajo.status.name }}
              </v-list-item-content>
            </v-list-item>

            <v-list-item>
              <v-list-item-content>Fecha de creación:</v-list-item-content>
              <v-list-item-content class="align-end">
                {{ trabajo.created_date }}
              </v-list-item-content>
            </v-list-item>

            <v-list-item>
              <v-list-item-content>Fecha modificada:</v-list-item-content>
              <v-list-item-content class="align-end">
                {{ trabajo.modified_date }}
              </v-list-item-content>
            </v-list-item>

            <v-list-item>
              <v-list-item-content>Inmueble:</v-list-item-content>
              <v-list-item-content class="align-end">
                {{ trabajo.property_office.name }} ({{
                  trabajo.property_office.client.name
                }})
              </v-list-item-content>
            </v-list-item>

            <v-list-item>
              <v-list-item-content>Estado:</v-list-item-content>
              <v-list-item-content class="align-end">
                {{ trabajo.property_office.province.name }}
              </v-list-item-content>
            </v-list-item>

            <v-list-item>
              <v-list-item-content>Proyecto:</v-list-item-content>
              <v-list-item-content class="align-end">
                {{ trabajo.proyect.name }}
              </v-list-item-content>
            </v-list-item>

            <v-list-item>
              <v-list-item-content>Concepto:</v-list-item-content>
              <v-list-item-content class="align-end">
                {{ trabajo.concept.name }}
              </v-list-item-content>
            </v-list-item>
             <v-list-item>
              <v-list-item-content>Mi trabajo:</v-list-item-content>
              <v-list-item-content class="align-end">
                <v-row>
                  <v-col cols="12" v-for="(archivo, i) in archivos" :key="i">
                    <v-card class="mx-auto" elevation="2" color="primary">
                      <v-card-actions>
                        <v-btn dark depressed small :href="archivo.file" color="blue-grey">
                          <v-icon left>
                            mdi-file
                          </v-icon>
                          {{ archivo.title }}
                        </v-btn>
                        <v-spacer />
                        <v-btn icon>
                          <v-icon>mdi-dots-vertical</v-icon>
                        </v-btn>
                        <br>
                      </v-card-actions>
                    </v-card>
                  </v-col>
                </v-row>
              </v-list-item-content>
            </v-list-item>
          </v-list>
        </v-card-text>
      </v-card>
    </v-col>
    <v-col cols="12" md="6" lg="6" sm="6">
      <v-card class="mx-auto">
        <v-card-subtitle>Comentarios</v-card-subtitle>
        <v-card-text v-for="(comentario, i) in trabajo.comments" :key="i">
          <div class="text--primary">{{ comentario }}</div>
        </v-card-text>
      </v-card>
    </v-col>
    <v-col cols="12" md="6" lg="6" sm="6">
      <v-card class="mx-auto" color="primary">
        <v-dialog transition="dialog-bottom-transition" max-width="600">
          <template v-slot:activator="{ on, attrs }">
            <v-btn v-bind="attrs" v-on="on">
              <v-icon left>mdi-plus</v-icon>
              Agregar trabajo
            </v-btn>
          </template>
          <template v-slot:default="dialog">
            <v-card :loading="loading ? 'red' : null">
              <v-toolbar color="indigo" dark>Agregar asignación</v-toolbar>
              <v-card-text>
                <v-file-input
                  v-model="PDFBase64"
                  accept=".pdf"
                  show-size
                  truncate-length="6"
                ></v-file-input>
              </v-card-text>
              <v-card-actions class="justify-end">
                <v-btn
                  text
                  color="success"
                  :disabled="!PDFBase64"
                  @click="subirArchivo()"
                  >Subir Archivo</v-btn
                >
                <v-btn text @click="dialog.value = false">Cerrar</v-btn>
              </v-card-actions>
            </v-card>
          </template>
        </v-dialog>
      </v-card>
    </v-col>
  </v-row>
</template>

<script>
export default {
  data: () => ({
    PDFBase64: null,
    loading: false,
    files: [],
  }),
  computed: {
    trabajo() {
      return this.$store.getters["MOD_trabajos/getTrabajo"];
    },
    archivos() {
      return this.$store.getters["MOD_trabajos/getArchivosTrabajo"];
    },
  },
  created() {
    this.obtenerTrabajos();
    this.TraerStatus();
    this.ObtenerUsuarios();
  },
  methods: {
    async obtenerTrabajos() {
      await this.$store.dispatch("MOD_trabajos/recuperarTrabajo", this.trabajo.id);
    },
    async TraerStatus() {
      await this.$store.dispatch("MOD_trabajos/obtenerStatusTrabajos");
    },
    async ObtenerUsuarios() {
      const flag_usuarios = await this.$store.dispatch(
        "MOD_usuarios/getUsuarios"
      );
      switch (flag_usuarios) {
        case 2:
          break;
        default:
          alert("Algo inesperado paso");
          break;
      }
    },
    async subirArchivo() {
      this.loading = true;
      const { error, message } = await this.$store.dispatch(
        "MOD_trabajos/AXN_SubirArchivo",
        {
          work: this.trabajo.id,
          PDFBase64: this.PDFBase64,
        }
      );

      if (error) {
        alert(message);
        return;
      }
      this.enviar();
      this.loading = false;
    },
    async enviar() {
      const {
        id,
        property_office,
        area_user,
        assigned_user,
        concept,
        proyect,
        status,
      } = this.trabajo;
      const { error, message } = await this.$store.dispatch(
        "MOD_trabajos/updatework",
        {
          id: id,
          status_id: status.id,
          project_id: proyect.id,
          concept_id: concept.id,
          property_office_id: property_office.id,
          assigned_user_id: assigned_user.id,
          area_user_id: area_user.id,
          send_review: true,
        }
      );
      if (error) {
        alert(message);
        return;
      }
      alert(message);
    },
  },
};
</script>
