import axios from '@/api';

import { json2localStorage } from '@/utils/data/store';

// import { INITIALISER_FUNCTION, LOGOUT_FUNCTION } from '@/store/constants';

export default {
  namespaced: true,

  state: {

    miembro: [],
    respuesta: null,
    usuarios: [],
    lista_asignaciones: [],

    usuarios_roles: [],
    usuarios_roles2: [],
    usuarios_roles3: [],

    asignaciones: [],
  },

  mutations: {
    setTrabajosAsignados(state, works) {
      json2localStorage('asignaciones', works);
      state.asignaciones = works;
    },

    MUT_EstablecerMiembro(state, miembro){
      json2localStorage('miembro', miembro)
      state.miembro = miembro
    },

    setUsuarios(state, resultado) {
      state.usuarios = resultado;
    },

    MUT_ObtenerUsuariosRol(state, resultado) {
      state.usuarios_roles = resultado;
    },
    MUT_ObtenerUsuariosRol2(state, resultado) {
      state.usuarios_roles2 = resultado;
    },
    MUT_ObtenerUsuariosRol3(state, resultado) {
      state.usuarios_roles3 = resultado;
    },
  },

  actions: {
    // [INITIALISER_FUNCTION]({ dispatch }) {
    //   dispatch('iniciarAutenticacion');
    // },
    // [LOGOUT_FUNCTION]({ commit }) {
    //   commit('limpiarTokens');
    // },
    // iniciarAutenticacion({ commit }) {
    //   const asignaciones = localStorage2json('asignaciones');
    //   commit('setTrabajosAsignados', asignaciones);
    // },

    async getUsuarios({ commit }) {
      try {
        const { data, status } = await axios.instance.get('usuarios/usuario/');
        const { items, message } = data;
        var usuarios = [];
        items.forEach((element) => {
          usuarios.push({
            'id': element.id,
            'nombre': element.name,
            'apellidos': element.last_name,
            'nombre_completo': element.name + ' ' + element.last_name,
            'usuario': element.username,
            'correo': element.email,
            'imagen': element.image,
            'is_active': element.is_active,
            'is_staff': element.is_staff,
            'is_superuser': element.is_superuser,
            'last_login': element.last_login,
          });
        });
        commit('setUsuarios', usuarios);
        return { 'mensaje': message, 'estado': status }
      } catch (error) {
        return { 'error': error }
      }
    },

    async AXN_ObtenerPersonalRol({ commit }, {rol}) {
      try {
        const { data } = await axios.instance.get(`usuarios/grupo/?rol=${rol}`);
        const { items } = data;
        var usuarios = [];
        items.forEach((element) => {
          usuarios.push({
            id_usuario: element.user.id,
            nombre_completo_usuario: element.user.name + ' ' + element.user.last_name,
            nombre_usuario: element.user.name,
            apellido_usuario: element.user.last_name,
            usuario_usuario: element.user.username,
            correo_usuario: element.user.email,
            imagen_usuario: element.user.image,
            is_active: element.user.is_active,
            is_staff: element.user.is_staff,
            is_superuser: element.user.is_superuser,
            last_login: element.user.last_login,
          });
        });

        if (rol == 'Administrador'){
          commit('MUT_ObtenerUsuariosRol', usuarios);
        }
        if (rol == 'Consultor'){
          commit('MUT_ObtenerUsuariosRol2', usuarios);
        }
        if (rol == 'Coordinador'){
          commit('MUT_ObtenerUsuariosRol3', usuarios);
        }
        return 'ok';
      } catch (error) {
        return 'Consulta no satisfactoria.'
      }
    },

    async AUX_CrearUsuaurio(context, { username, email, password, name, last_name, is_staff }) {
      try {
        const { data, status } = await axios.instance.post('/usuarios/usuario/', {
          username,
          email,
          password,
          name,
          last_name,
          is_staff,
        });
        const { items, message } = data;
        // commit('setUser', {
        //   user: items,
        // });
        return { message, status, id: items.id };
      } catch (error) {
        const strMessage = String(error.response.data.message);
        const strError = error.response.data.error;
        return { message: strMessage, error: strError, };
      }
    },
    async AUX_ActualizarUsuaurio(context, { id, username, email, password, name, last_name, is_staff, groups }) {
      try {
        const { data, status } = await axios.instance.put(`/usuarios/usuario/${id}/`, {
          username,
          email,
          password,
          name,
          last_name,
          is_staff,
          groups
        });
        const { message } = data;
        // commit('setUser', {
        //   user: items,
        // });
        return { message, status };
      } catch (error) {
        const strMessage = String(error.response.data.message);
        const strError = String(error.response.data.error);
        const status = error.response.status
        return { message: strMessage, error: strError, status };
      }
    },
    async AXN_TraerMiembro({commit}, {id}){
      try {
        const { data } = await axios.instance.get(`/usuarios/usuario/${id}/`)
        const { items, message } = data
        commit('MUT_EstablecerMiembro', {items})
        return { message }
      } catch(error) {
        const strMessage = String(error.response.data.message);
        const strError = String(error.response.data.error);
        return { message: strMessage, error: strError };
      }
    },
  },
  getters: {
    getUsuarios: (state) => state.usuarios,
    GET_ObtenerMiembro: (state) => state.miembro,
    obtenerAsignaciones: (state) => state.asignaciones,
  },
};
