import axios from '../api'

const state = {
  proyectos: [],
  proyecto: {},

  conceptos: [],
  concepto: {},

  cargando: false,
  cargandoConceptos: false
}

const mutations = {
  setProyectos(state, payload) {
    state.proyectos = payload
  },
  setProyecto(state, payload) {
    state.proyecto = payload
  },
  clearProyecto(state){
    state.proyecto = {}
    state.concepto = {}
    state.conceptos = []
  },
  setCargando(state, payload) {
    state.cargando = payload
  },
  setConceptos(state, payload) {
    state.conceptos = payload
  },
  setConcepto(state, payload) {
    state.concepto.push(payload)
  },
  setCargandoConceptos(state, payload) {
    state.cargandoConceptos = payload
  },
  clearConcepto(state){
    state.concepto = {}
  },
}

const actions = {
  async obtenerProyectos({ commit }) {
    commit('setCargando', true)
    try {
      const { data } = await axios.instance.get('/proyectos/proyecto/')
      const { items, message } = data

      const proyectos = []
      items.forEach(element => {
        proyectos.push({
          id: element.id,
          nombre: element.name
        })
      })
      commit('setProyectos', proyectos)
      commit('setCargando', false)
      return { 'mensaje': message }
    } catch (error) {
      commit('setCargando', false)
      return { 'error': error.response.data.errors }
    }
  },
  async obtenerProyecto({ state, commit }, {id}) {
    commit('setCargando', true)
    const proyecto = state.proyectos.find(proyecto => proyecto.id == id)
    commit('setProyecto', proyecto)
    commit('setCargando', false)
  },
  async crearProyecto({state, commit}, nombre ) {
    commit('setCargando', true)
    try {
      const { data, status } = await axios.instance.post('proyectos/proyecto/', {
        'name': nombre
      })
      const { items, message } = data
      const proyecto = {
        'id': items.id,
        'nombre': items.name
      }
      commit('setProyecto', proyecto)
      state.proyectos.push(proyecto)
      commit('setCargando', false)
      return { 'mensaje': message, 'estado': status }
    } catch (error) {
      commit('setCargando', false)
      return { 'error': error }
    }
  },
  async crearConcepto({commit, state}, { nombre, proyecto }) {
    try {
      commit('setCargandoConceptos', true)
      const { data, status } = await axios.instance.post('proyectos/conceptos/', {
        'name': nombre, 'project': proyecto
      })
      const { items, message } = data
      const concepto = { 'id': items.id, 'nombre': items.name }
      state.conceptos.push(concepto)
      commit('setCargandoConceptos', false)
      return { 'mensaje': message, 'estado': status }
    } catch (error) {
      return { error: error.response.data.errors.name[0] }
    }
  },
  async obtenerConceptos({ commit }, id) {
    commit('setCargandoConceptos', true)
    try {
      const { data, status } = await axios.instance.get(`proyectos/proyecto/${id}/`)
      const { items, message } = data
      const { concepts } = items

      var conceptos = []
      concepts.forEach(element => {
        conceptos.push({
          id: element.id,
          nombre: element.name,
        })
      })
      commit('setConceptos', conceptos)
      commit('setCargandoConceptos', false)
      return { 'mensaje': message, 'estado': status }
    } catch (error) {
      commit('setCargandoConceptos', false)
      return { 'error': error }
    }
  },
}

const getters = {
  getCargando: (state) => state.cargando,
  getCargandoConceptos: (state) => state.cargandoConceptos,
  getProyectos: (state) => state.proyectos,
  getProyecto: (state) => state.proyecto,
  getConceptos: (state) => state.conceptos
}

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters
}
