import axios from '@/api'

export default {
  namespaced: true,
  state: {
    estado: {},
    estados: [],
    isCargandoEstados: false,

    municipio: {},
    municipios: [],
    isCargandoMunicipios: false,
    flag_obtener_municipios_de_estado: 0,


    localidad: {},
    localidades: [],
    isCargandoLocalidades: false
  },
  mutations: {
    setEstado(state, payload) { state.estado = payload },
    setEstados(state, payload) { state.estados = payload },
    setCargandoEstados(state, boolean) { state.isCargandoEstados = boolean },

    setMunicipio(state, payload) { state.municipio = payload },
    setMunicipios(state, payload) { state.municipios = payload },
    setCargandoMunicipios(state, boolean) { state.isCargandoMunicipios = boolean },
    limpiarMunicipios(state) { state.municipios = [] },

    setLocalidad(state, payload) { state.localidad = payload },
    setLocalidades(state, payload) { state.localidades = payload },
    setCargandoLocalidades(state, boolean) { state.isCargandoLocalidades = boolean },
    limpiarLocalidades(state) { state.localidades = [] },
  },
  actions: {
    async obtenerEstados({ commit }) {
      try {
        commit('setCargandoEstados', true)
        const { data, status } = await axios.instance.get('territorios/estados/')
        const { items, message } = data
        var estados = []
        items.forEach(element => {
          estados.push({
            id: element.id,
            nombre: element.name,
            abreviatura: element.abbreviation,
          })
        })
        commit('setEstados', estados)
        commit('setCargandoEstados', false)
        return { 'mensaje': message, 'estado': status }
      } catch (error) {
        commit('setCargandoEstados', false)
        return {'error': error}
      }
    },
    async obtenerEstado({ commit }, payload) {
      await commit('setEstado', payload)
    },
    async obtenerMunicipio({ commit }, payload) {
      await commit('setMunicipio', payload)
    },
    async obtenerLocalidad({ commit }, payload) {
      await commit('setLocalidad', payload)
    },
    async obtenerMunicipiosDeEstado({ commit }, estado ) {
      try {
        commit("setCargandoMunicipios", true)
        const { data } = await axios.instance.get(`territorios/municipios/?province=${estado}`)
        const { items, status } = data
        var municipios = []
        items.forEach(element => {
          municipios.push({
            id: element.id,
            nombre: element.name,
          })
        })
        commit('setMunicipios', municipios)
        commit('setCargandoMunicipios', false)
        return { 'mensaje': 'Consulta satisfactoria.', 'status': status }
      } catch (error) {
        commit('setCargandoMunicipios', false)
        commit('limpiarMunicipios')
        return { 'mensaje': error.response.data.message, 'status': 404 }
      }
    },
    async obtenerLocalidadesDeMunicipio({commit}, municipio ) {
      try {
        const { data } = await axios.instance.get(`territorios/localidades/?municipality=${municipio}`)
        const { items, status } = data
        var localidades = []
        items.forEach(element => {
          localidades.push({
            id: element.id,
            nombre: element.name,
          })
        })
        commit('setLocalidades', localidades)
        commit('setCargandoLocalidades', false)
        return { 'mensaje': 'Consulta satisfactoria.', 'status': status }
      } catch (error) {
        commit('setCargandoLocalidades', false)
        commit('limpiarLocalidades')
        return { 'mensaje': error.response.data.message, 'status': error.response.status }
      }
    },
    async obtenerMunicipiosDeInmueblesExistentes({ commit }, { cliente, estado }) {
      try {
        commit('setCargandoMunicipios', true)
        const { data } = await axios.instance.get(`territorios/municipios/sucursales/?clients=${cliente}&province=${estado}`)
        const { items, status } = data
        var municipios = []
        items.forEach(element => {
          municipios.push({
            id: element.id,
            nombre: element.name,
          })
        })
        commit('setMunicipios', municipios)
        commit('setCargandoMunicipios', false)
        return { 'mensaje': 'Consulta satisfactoria.', 'status': status }
      } catch (error) {
        commit('setCargandoMunicipios', false)
        commit('limpiarMunicipios')
        return { 'mensaje': error.response.data.message, 'status': error.response.status }
      }
    },
    async obtenerLocalidadesDeInmueblesExistentes({ commit }, { cliente, estado, municipio }) {
      try {
        commit('setCargandoLocalidades', true)
        const { data, status } = await axios.instance.get(`territorios/localidades/sucursales/?clients=${cliente}&province=${estado}&municipality=${municipio}`)
        const { items } = data
        var localidades = []
        items.forEach(element => {
          localidades.push({
            'id': element.id,
            'nombre': element.name,
          })
        })
        commit('setLocalidades', localidades)
        commit('setCargandoLocalidades', false)
        return { 'mensaje': 'Consulta satisfactoria.', 'estado': status }
      } catch (error) {
        commit('limpiarLocalidades')
        commit('setCargandoLocalidades', false)
        return { 'error': error }
      }
    }
  },
  getters: {
    getEstado: (state) => state.estado,
    getEstados: (state) => state.estados,
    getCargandoEstados: (state) => state.isCargandoEstados,
    getMunicipio: (state) => state.municipio,
    getMunicipios: (state) => state.municipios,
    getCargandoMunicipios: (state) => state.isCargandoMunicipios,
    getLocalidad: (state) => state.localidad,
    getLocalidades: (state) => state.localidades,
    getCargandoLocalidades: (state) => state.isCargandoLocalidades
  }
}