<template>
  <v-card outlined class="mx-auto">
    <v-card-title class="grey lighten-2">Datos generales de la solicitud de recurso</v-card-title>
    <br>
    <v-card-text>
      <v-form v-model="valid" ref="formulario" lazy-validation>
        <v-row dense>
          <v-col cols="12" sm="4">
            <v-combobox v-model="solicitud_recurso.tipo_pago"
              :items="pagos"
              label="Tipo de pago"
              :rules="[(v) => !!v || 'Es necesario elegir el tipo de pago']"
              outlined
              clearable
            />
          </v-col>
          <v-col cols="12" sm="4">
            <v-text-field v-model="solicitud_recurso.banco"
              label="Banco" :rules="[(v) => !!v || 'Es necesario escribir el banco']" outlined
            />
          </v-col>
          <v-col cols="12" sm="4">
            <v-text-field type="number" label="Importe" :value=total disabled outlined />
          </v-col>
          <v-col cols="12" sm="12">
            <v-textarea
              v-model="solicitud_recurso.concepto_pago"
              dense
              outlined
              label="Concepto de pago"
              :rules="[(v) => !!v || 'Es necesario escribir los datos bancarios']"
              auto-grow
              rows="1"
              row-height="15"
            />
          </v-col>
          <v-col cols="12" sm="12">
            <v-simple-table dense>
              <template v-slot:top>
                <v-card-title class="grey lighten-2">
                  ¿Hacer peticiones en una sola solicitud de recurso?
                  <v-spacer />
                  <v-checkbox v-model="solicitud_recurso.multiple" :label="`${solicitud_recurso.multiple ? 'Si' : 'No'}`"></v-checkbox>
                </v-card-title>
              </template>
              <template v-slot:default>
                <thead>
                  <tr>
                    <th class="text-left"> Proyecto </th>
                    <th class="text-left"> Concepto </th>
                    <th class="text-left"> Inmueble </th>
                    <th class="text-left"> Importe </th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(peticion, index) in peticiones_verificadas" :key="peticion.trabajo">
                    <td>{{ trabajos_seleccionados[index].proyecto }}</td>
                    <td>{{ trabajos_seleccionados[index].concepto }}</td>
                    <td>{{ trabajos_seleccionados[index].inmueble.nombre }}</td>
                    <td>$ {{peticion.monto}}</td>
                  </tr>
                </tbody>
              </template>
            </v-simple-table>
          </v-col>
        </v-row>
      </v-form>
    </v-card-text>
  </v-card>
</template>

<script>
export default {
  data:() =>({
    pagos: ["Transferencia", "Cheque"],
    continuar_p4: false,
    solicitud_recurso: {
      tipo_pago: '',
      banco: '',
      concepto_pago: '',
      multiple: false,
    },
  }),
  computed: {
    peticiones_verificadas() { return this.$store.getters["MOD_pagos/getPeticionesVerificadas"] },
    trabajos_seleccionados() { return this.$store.getters["MOD_trabajos/getTrabajosSeleccionados"]; },
    valid: {
      get(){ return this.continuar_p4 },
      set(nuevoValor){
        this.$emit("eventoContinuar", { 'isValid_p3': nuevoValor, 'validacion': this.$refs.formulario})
        if (this.$refs.formulario) { this.$store.commit("MOD_pagos/setSolicitudRecurso", this.solicitud_recurso) }
      }
    },
    total() {
      var total = 0.0
      for(const peticion of this.peticiones_verificadas){
        total = peticion.monto ? Number(peticion.monto) + total : 0.0 + total
      }
      return total.toFixed(2)
    },
  },
};
</script>