<template>
  <v-container fluid>
    <v-app-bar v-for="comentario in comentarios" :key="comentario.id" color="rgba(0,0,0,0)" flat class="mb-16">
      <v-card v-if="usuario == comentario.usuario.id" class="mt-10 mr-2" max-width="350px" color="blue" dark>
        <v-list-item three-line>
          <v-list-item-content>
            <div class="mb-4">{{comentario.comentario}}</div>
            <v-list-item-subtitle>Hace 16 mins
              <span class="ml-16">Visto 1:03PM</span>
            </v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>
      </v-card>
      <v-badge v-else bordered bottom color="grey" dot offset-x="16" offset-y="9">
        <v-avatar class="mt-n5 mr-2" size="30" elevation="10">
          <img src="https://content.throwbacks.com/images/2017/11/panthro8.JPG" />
        </v-avatar>
      </v-badge>
      <v-badge v-if="usuario == comentario.usuario.id" bordered bottom color="green" dot offset-x="10" offset-y="10">
        <v-avatar class="mt-n5" size="30" elevation="10">
          <img src="https://i.ytimg.com/vi/MZr4KirBYtQ/hqdefault.jpg" />
        </v-avatar>
      </v-badge>
      <v-card v-else class="mt-10" max-width="350px">
        <v-list-item three-line>
          <v-list-item-content>
            <div class="mb-4">
                {{comentario.comentario}}
            </div>
            <v-list-item-subtitle>Hace 12 mins</v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>
      </v-card>
    </v-app-bar>
    <v-app-bar color="rgba(0,0,0,0)" flat>
      <v-text-field
      :loading="cargando"
        v-model="comentario"
        append-outer-icon="mdi-send"
        filled
        clear-icon="mdi-close-circle"
        clearable
        label="Message"
        type="text"
        @click:append-outer="sendMessage"
        @click:clear="clearMessage"
      ></v-text-field>
    </v-app-bar>
  </v-container>
</template>
<script>
export default {
  data: () => ({
    comentario: "",
  }),
  computed:{
    comentarios(){ return this.$store.getters["MOD_pagos/getComentarios"] },
    usuario(){ return this.$store.getters["MOD_autenticacion/obtenerIdUsuario"] },
    recurso() { return this.$store.getters["MOD_pagos/getRecurso"] },
    cargando() { return this.$store.getters["MOD_pagos/getCargando"]; }
  },
  methods: {
    async sendMessage() {
      const {id} = this.recurso
      if(this.comentario){
        const comentario = { 'recurso': id, 'usuario': this.usuario, 'comentario':this.comentario }
        const { mensaje, estado, error } = await this.$store.dispatch("MOD_pagos/hacerComentario", comentario)
        if (error) {
          this.$store.commit("alerta/addAlertas", { 'estado': estado, 'mensaje': mensaje, 'error': error });
        }else {
          this.clearMessage();
        }
      }
    },
    clearMessage() {
      this.comentario = "";
    },
  },
};
</script>