<template>
  <v-dialog transition="dialog-top-transition" max-width="500" persistent>
    <template v-slot:activator="{ on, attrs }">
      <v-btn color="primary" v-bind="attrs" v-on="on" icon fab>
        <v-icon>mdi-plus</v-icon>
      </v-btn>
    </template>
    <template v-slot:default="dialog">
      <v-card class="mx-auto" elevation="5" :loading="cargando">
        <v-card-title class="grey lighten-2">
          Crear un nuevo proyecto
          <v-spacer />
          <v-btn @click="cerrrarDialogo(dialog)" color="error" icon><v-icon>mdi-close</v-icon></v-btn>
        </v-card-title>
        <v-divider />
        <v-card-text>
          <v-text-field v-model="nombre" label="Nombre del proyecto" />
        </v-card-text>
        <v-card-actions class="justify-end">
          <v-btn @click="crear(dialog)" color="green" outlined :disabled="!nombre">Crear</v-btn>
        </v-card-actions>
      </v-card>
    </template>
  </v-dialog>
</template>

<script>
export default {
  data: () => ({
    nombre: null,
  }),
  computed: {
    cargando() { return this.$store.getters["proyecto/getCargando"] }
  },
  methods: {
    async crear(dialog) {
      const { nombre } = this
      const { mensaje, estado, error } = await this.$store.dispatch("proyecto/crearProyecto", nombre );
      this.$store.commit("alerta/addAlertas", { "estado": estado, "mensaje": mensaje, "error": error })
      if(!error){
        this.cerrrarDialogo(dialog)
      }
    },
    cerrrarDialogo(dialog){
      dialog.value = false
      this.nombre = null
    }
  },
};
</script>
