
const state = {
  alertas: []
}
const mutations = {
  setAlertas(state, payload) { state.alertas = payload },
  addAlertas(state, payload) { state.alertas.push(payload) },
  clearAleras(state) { state.alertas = [] }
}

const actions = {

}

const getters = {
  getAlertas: (state) => state.alertas
}

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters
}
