<template>
  <v-main class="grey">
    <v-container fluid>
      <v-toolbar class="mb-2" color="indigo darken-5" dark flat>
        <v-toolbar-title>Estado del trabajo</v-toolbar-title>
      </v-toolbar>

      <v-row>
        <v-col cols="12" sm="12" md="12">
          <v-card>
            <v-card-title class="subheading font-weight-bold">
              Estado del trabajo
            </v-card-title>

            <v-divider></v-divider>

            <v-list dense>
              <v-list-item>
                <v-list-item-content>Status:</v-list-item-content>
                <v-list-item-content class="align-end">
                  {{ trabajo.status.name }}
                </v-list-item-content>
              </v-list-item>

              <v-list-item>
                <v-list-item-content>Asignado:</v-list-item-content>
                <v-list-item-content class="align-end">
                  {{ trabajo.created_date }}
                </v-list-item-content>
              </v-list-item>

              <v-list-item>
                <v-list-item-content>Última actualización:</v-list-item-content>
                <v-list-item-content class="align-end">
                  {{ trabajo.modified_date }}
                </v-list-item-content>
              </v-list-item>

              <v-list-item>
                <v-list-item-content>Inmueble:</v-list-item-content>
                <v-list-item-content class="align-end">
                  {{ trabajo.property_office.name }}
                </v-list-item-content>
              </v-list-item>

              <v-list-item>
                <v-list-item-content>Proyecto:</v-list-item-content>
                <v-list-item-content class="align-end">
                  {{ trabajo.proyect.name }}
                </v-list-item-content>
              </v-list-item>

              <v-list-item>
                <v-list-item-content>Concepto:</v-list-item-content>
                <v-list-item-content class="align-end">
                  {{ trabajo.concept.name }}
                </v-list-item-content>
              </v-list-item>

              <v-list-item>
                <v-list-item-content>Asignado:</v-list-item-content>
                <v-list-item-content class="align-end">
                  {{ trabajo.area_user.name }} {{ trabajo.area_user.last_name }}
                </v-list-item-content>
              </v-list-item>

              <v-list-item>
                <v-list-item-content>Ácargo:</v-list-item-content>
                <v-list-item-content class="align-end">
                  {{ trabajo.assigned_user.name }} {{ trabajo.area_user.last_name }}
                </v-list-item-content>
              </v-list-item>
            </v-list>

            <v-tabs
              v-model="tab"
              background-color="deep-purple accent-4"
              centered
              dark
              icons-and-text
            >
              <v-tabs-slider></v-tabs-slider>

              <v-tab href="#tab-1">
                Archivo
                <v-icon>mdi-file-pdf</v-icon>
              </v-tab>

              <v-tab href="#tab-2">
                Comentario
                <v-icon>mdi-comment-multiple-outline</v-icon>
              </v-tab>
            </v-tabs>

            <v-tabs-items v-model="tab">
              <v-tab-item :value="'tab-' + 1">
                <v-card class="mx-auto">
                  <v-card-text v-if="!Boolean(trabajo.files.length)" class="pt-4">
                    No han subido archivos.
                  </v-card-text>
                  <v-virtual-scroll
                    :items="trabajo.files"
                    :item-height="50"
                  >
                    <template v-slot:default="{ item }">
                      <v-list-item>
                        <v-list-item-content>
                          <v-list-item-title>{{ item.title }}</v-list-item-title>
                        </v-list-item-content>

                        <v-list-item-action>
                          <v-btn
                            depressed
                            small
                            :href="item.file"
                          >
                            Ver archivo
                            <v-icon
                              color="orange darken-4"
                              right
                            >
                              mdi-open-in-new
                            </v-icon>
                          </v-btn>
                        </v-list-item-action>
                      </v-list-item>
                    </template>
                  </v-virtual-scroll>
                </v-card>
              </v-tab-item>
              <v-tab-item :value="'tab-' + 2">
                <Comentarios />
              </v-tab-item>
            </v-tabs-items>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
  </v-main>
</template>

<script>
  import Comentarios from "@/components/Comentarios/Comentarios.vue";
  export default {
    components: {
      Comentarios,
    },
    data: () => ({
      tab: null,
      bottonEnviar: false,
      files: [],
    }),
    computed: {
      trabajo() {
        return this.$store.getters["MOD_trabajos/getTrabajo"];
      },
      grups() {
        return this.$store.getters["MOD_autenticacion/getGrupos"];
      },
    },
    created() {
      this.obtenerTrabajo();
    },
    methods: {
      async obtenerTrabajo() {
        await this.$store.dispatch("MOD_trabajos/recuperarTrabajo",
          this.trabajo.id
        );
      },
    },
  };
</script>
