<template>
  <v-row justify="center">
    <v-col cols="12" sm="2">
      <v-sheet color="grey lighten-4" class="pa-4">
        <h1><b>Recursos</b></h1>
      </v-sheet>
      <v-list>
        <v-list-item-group v-model="recursoSeleccionado">
          <v-list-item v-for="[text] in recursos" :key="text" link>
            <v-list-item-content >
              <v-list-item-title>{{ text }}</v-list-item-title>
            </v-list-item-content>
            <v-list-item-action>{{recursos_n.length}}</v-list-item-action>
          </v-list-item>
        </v-list-item-group>
      </v-list>
    </v-col>
    <v-col cols="12" sm="6">
      <peticiones :isConfirmarRecurso="isConfirmarRecurso" />
    </v-col>
  </v-row>
</template>

<script>
import Peticiones from "../../components/Pago/ValidarPeticiones.vue";

export default {
  data: () => ({
    recursos: [
      ["Solicitudes de recursos"],
      ["Recursos"],
      ["Transferencias"],
      ["Cheques"],
    ],
    recurso: 0,
    isConfirmarRecurso: true,
    icon: "creado"
  }),
  components: {
    peticiones: Peticiones
  },
  computed:{
    recursos_n() { return this.$store.getters["MOD_pagos/getRecursos"]; },
    recursoSeleccionado:{
      get(){ return this.recurso },
      set(nuevoValor){
        const recurso = nuevoValor === undefined ? this.recurso : nuevoValor
        this.recurso = recurso
        if (this.recurso === 0) { this.inicializarRecursos({solicitud:"False"})
          this.isConfirmarRecurso=true
        } else if (this.recurso===1) { this.inicializarRecursos({solicitud:"True"}, this.isConfirmarRecurso=false)
        } else if (this.recurso===2) { this.inicializarRecursos({tipo_pago:"Transferencia"}, this.isConfirmarRecurso=false)
        } else { this.inicializarRecursos({tipo_pago:"Cheque"}), this.isConfirmarRecurso=false}
      }
    },
    usuario() { return this.$store.getters["MOD_autenticacion/obtenerIdUsuario"]; },
  },
  created(){
    this.inicializarRecursos({solicitud:"False"});
  },
  methods: {
    async inicializarRecursos({solicitud, tipo_pago }) {
      const { mensaje, estado, error } = await this.$store.dispatch("MOD_pagos/recuperarRecursosUsuario", {
        solicitud: solicitud, gestor: this.usuario, tipo_pago: tipo_pago
      });
      if (error) {
        this.$store.commit("alerta/addAlertas", { estado: estado, mensaje: mensaje, error: error, });
      }
    },
  }
};
</script>