<template>
  <v-row dense justify="center">
    <v-col cols="12" sm="9" md="9" lg="9" xl="9">
      <v-stepper v-model="e1">
        <v-stepper-header>
          <v-stepper-step :complete="e1 > 1" step="1">
            Paso 1 <br />
            Inmuebles
          </v-stepper-step>

          <v-divider />

          <v-stepper-step :complete="e1 > 2" step="2">
            Paso 2 <br />
            Datos genereales
          </v-stepper-step>

          <v-divider />

          <v-stepper-step step="3">
            Paso 3 <br />
            Asignar
          </v-stepper-step>
        </v-stepper-header>

        <v-stepper-items>
          <v-stepper-content step="1">
            <inmuebles-tabla />
            <br />
            <v-btn
              color="success"
              block
              @click="e1 = 2"
              :disabled="isInmueblesSeleccionados"
            >
              Continue
            </v-btn>
          </v-stepper-content>

          <v-stepper-content step="2">
            <v-btn color="warning" block @click="e1 = 1"> Regresar </v-btn>
            <br />
            <formulario-crear-trabajo />
            <br />
            <v-btn
              color="primary"
              @click="e1 = 3"
              block
              :disabled="!asignaciones_v.length"
            >
              Continue
            </v-btn>
          </v-stepper-content>
          <v-stepper-content step="3">
            <v-btn color="warning" block @click="e1 = 2"> Regresar </v-btn>
            <br />
            <CompPru12 />
            <br />

            <v-btn color="primary" block @click="asignar()">
              Finalizar
            </v-btn>
          </v-stepper-content>
        </v-stepper-items>
      </v-stepper>
    </v-col>
    <v-col cols="12" sm="3" md="3" lg="3" xl="3">
      <v-card class="mx-auto" outlined>
        <v-card-title>Resumen </v-card-title>
        <div class="text-end">
          <v-btn rounded color="error" @click="$router.go(-1)">
            Cancelar
            <v-icon right>mdi-close-circle-outline</v-icon>
          </v-btn>
        </div>
      </v-card>
    </v-col>
  </v-row>
</template>

<script>
import InmueblesTable from "../../components/TablaBuscarTrabajosInmueble.vue";
import FromularioCrearTrabajo from "../../components/trabajo/FormularioTrabajo.vue";
import CompPru12 from "../../components/TablaTrabajoInmuebles.vue";

export default {
  components: {
    'inmuebles-tabla': InmueblesTable,
    'formulario-crear-trabajo':FromularioCrearTrabajo,
    CompPru12,
  },
  data: () => ({
    e1: 1,
    inmuebles: [],
    isValid_p2: false,
  }),
  computed: {
    asignaciones() {
      return this.$store.getters["MOD_trabajos/getAsignaciones"];
    },
    asignaciones_v() {
      return this.$store.getters["MOD_trabajos/getAsignacionesVerificadas"];
    },
    isInmueblesSeleccionados() {
      return this.$store.getters["MOD_inmuebles/getInmueblesSeleccionados"].length ? false : true;
    },
  },
  methods: {
    async asignar() {
      const { asignaciones_v } = this;
      const { error, estado, mensaje } = await this.$store.dispatch(
        "MOD_trabajos/confirmarAsignacion",
        asignaciones_v
      );
      this.$store.commit("alerta/addAlertas", {
        estado: estado,
        mensaje: mensaje,
        error: error,
      });
      if(!error){
        this.$store.commit("MOD_inmuebles/cleanInmueblesSeleccionados")
        this.$store.commit("MOD_trabajos/cleanAsignacionesVerificadas")
        this.$router.push({"name": "trabajos-inmuebles"})
      }
    },
    setValidP2({validacion}){
      this.isValid_p2 = validacion ? validacion.validate() : false
    }
  },
};
</script>
