<template>
  <v-dialog transition="dialog-top-transition" max-width="500" persistent>
    <template v-slot:activator="{ on, attrs }">
      <v-btn color="primary" v-bind="attrs" v-on="on" icon fab :disabled="!isProyecto" >
        <v-icon>mdi-plus</v-icon>
      </v-btn>
    </template>
    <template v-slot:default="dialog">
      <v-card class="mx-auto" elevation="5" :loading="cargando">
        <v-card-title class="grey lighten-2">
          Crear un concepto nuevo para {{ proyecto.nombre }}
          <v-spacer />
          <v-btn @click="cerrrarDialogo(dialog)" color="error" icon>
            <v-icon>mdi-close</v-icon></v-btn>
        </v-card-title>
         <v-divider />
        <v-card-text>
          <v-text-field v-model="concepto" label="Nombre del concepto" />
        </v-card-text>
        <v-card-actions class="justify-end">
          <v-btn text @click="create(dialog)" color="green" outlined :disabled="!concepto">Crear</v-btn>
        </v-card-actions>
      </v-card>
    </template>
  </v-dialog>
</template>

<script>
export default {
  data: () => ({
    concepto: null,
  }),
  computed: {
    proyecto() { return this.$store.getters['proyecto/getProyecto'] },
    isProyecto() { return Boolean(Object.keys(this.proyecto).length) },
    cargando() { return this.$store.getters["proyecto/getCargando"] }
  },
  methods: {
    async create(dialog) {
      const { concepto, proyecto } = this;
      const { mensaje, estado, error, } = await this.$store.dispatch("proyecto/crearConcepto", {
        'nombre': concepto,
        'proyecto': proyecto.id,
      });
      this.$store.commit("alerta/addAlertas", { "estado": estado, "mensaje": mensaje, "error": error })
      if(!error){
        this.cerrrarDialogo(dialog)
      }
    },
    cerrrarDialogo(dialog){
      dialog.value = false
      this.concepto = null
    }
  },
};
</script>
