const localStorage2json = (propertyName) => {
  const stringProperty = localStorage.getItem(propertyName);
  try {
    return JSON.parse(stringProperty);
  } catch (error) {
    console.error({ error, propertyName, stringProperty });
    return null;
  }
};

const json2localStorage = (name, jsonData) => {
  const stringData = JSON.stringify(jsonData);
  localStorage.setItem(name, stringData);
};

export {
  localStorage2json,
  json2localStorage,
};
