<template>
  <v-row dense>
    <v-col cols="auto">
      <v-autocomplete
        v-model="estado"
        label="Estados"
        item-text="nombre"
        :items="estados"
        :disabled="isEstados"
        item-value="id"
        dense
        clearable
        :loading="isCargandoEstados"
        return-object
      ></v-autocomplete>
    </v-col>
    <v-col cols="auto">
      <v-autocomplete
        v-model="municipio"
        label="Municipios"
        item-text="nombre"
        :items="municipios"
        :disabled="isMunicipios"
        item-value="id"
        dense
        :loading="isCargandoMunuicipios"
        clearable
        return-object
      ></v-autocomplete>
    </v-col>
    <v-col cols="auto">
      <v-autocomplete
        v-model="localidad"
        label="Localidades"
        item-text="nombre"
        :items="localidades"
        :disabled="isLocalidades"
        item-value="id"
        dense
        :loading="isCargandoLocalidades"
        clearable
        return-object
      ></v-autocomplete>
    </v-col>
  </v-row>
</template>

<script>
export default {
  data: () => ({
    localidad: null
  }),
  computed: {
    cliente() { return this.$store.getters["MOD_clientes/getCliente"].id },
    estados() { return this.$store.getters["MOD_ubicaciones/getEstados"] },
    estado: {
      get(){ return this.$store.getters["MOD_ubicaciones/getEstado"] },
      async set(nuevoValor){
        await this.$store.dispatch("MOD_ubicaciones/obtenerEstado", nuevoValor)
        if (nuevoValor != null) {
          const { id } = nuevoValor
          const {error} = await this.Buscar({ cliente: this.cliente, estado: id, municipio: '', localidad: '', })
          if (!error){ this.obtenerMunicipiosDeEstado(this.cliente, id ) }
        } else {
          this.Buscar({ cliente: this.cliente, estado: '', municipio: '', localidad: '', })
          this.$store.commit("MOD_ubicaciones/limpiarMunicipios")
        }
      }
    },
    isEstados() { return this.estados.length ? false : true },
    isCargandoEstados() { return this.$store.getters["MOD_ubicaciones/getCargandoEstados"] },
    municipios() { return this.$store.getters["MOD_ubicaciones/getMunicipios"] },
    municipio: {
      get(){ return this.$store.getters["MOD_ubicaciones/getMunicipio"] },
      async set(nuevoValor){
        await this.$store.dispatch("MOD_ubicaciones/obtenerMunicipio", nuevoValor)
         if (nuevoValor != null) {
          const { id } = nuevoValor
          const {error} = await this.Buscar({ cliente: this.cliente, estado: this.estado.id, municipio: id, localidad: '', })
          if (!error){ this.obtenerLocalidadesDeMunicipio({ 'cliente': this.cliente, 'estado': this.estado.id, 'municipio': id })}
        } else {
          this.Buscar({ cliente: this.cliente, estado: '', municipio: '', localidad: '', })
        }
      }
    },
    isMunicipios() { return this.municipios.length ? false : true },
    isCargandoMunuicipios() { return this.$store.getters["MOD_ubicaciones/getCargandoMunicipios"] },
    localidades() { return this.$store.getters["MOD_ubicaciones/getLocalidades"]},
    isLocalidades() { return this.localidades.length ? false : true },
    isCargandoLocalidades() { return this.$store.getters["MOD_ubicaciones/getCargandoLocalidades"] },
  },
  created() {
    this.inicializarEstados()
  },
  methods: {
    async inicializarEstados() {
      await this.$store.dispatch("MOD_ubicaciones/obtenerEstados")
    },
    async obtenerMunicipiosDeEstado( cliente, estado ) {
      const { mensaje, error, status } = await this.$store.dispatch("MOD_ubicaciones/obtenerMunicipiosDeInmueblesExistentes",
        {cliente, estado}
      )
      if (error){
        this.$store.commit("alerta/addAlertas", { 'mensaje': mensaje, 'estado': status, 'error': error })
      }
    },
    async obtenerLocalidadesDeMunicipio({ cliente, estado, municipio }) {
      const {mensaje, estadoHttp, error} = await this.$store.dispatch(
        "MOD_ubicaciones/obtenerLocalidadesDeInmueblesExistentes", { cliente, estado, municipio }
      )
      if (error) {
        this.$store.commit("alerta/addAlertas", { 'mensaje': mensaje, 'estado': estadoHttp, 'error': error })
      }
    },
    async Buscar({ cliente, estado, municipio, localidad }) {
      const { mensaje, estadoHttp, error } = await this.$store.dispatch("MOD_inmuebles/listarPorClientesTerritorio", {
        'cliente': [cliente],
        'estado': estado,
        'municipio': municipio,
        'localidad': localidad
      })
      if (error) {
        this.$store.commit("alerta/addAlertas", { 'mensaje': mensaje, 'estado': estadoHttp, 'error': error })
      }
      return {'error': error}
    },
  },
};
</script>
