<template>
  <v-data-table
    v-model="trabajos_seleccionados"
    class="elevation-5"
    :loading="cargando"
    :headers="headers"
    :items="trabajos"
    :items-per-page="-1"
    loading-text="Cargando... por favor espere"
    :footer-props="{
      itemsPerPageText: 'Filas por página',
      itemsPerPageAllText: 'Todos',
      pageText: 'p - Tp de Fp',
    }"
    no-data-text="No tienes trabajos."
    :show-select="mostrar_seleccionados"
    :single-select="singleSelect"
  >
    <!-- @click:row="verTrabajo" -->
    <template v-slot:top>
      <v-toolbar flat class="grey lighten-2">
        <v-toolbar-title>
          {{ trabajos.length }}
          <span v-text="trabajos.length == 1 ? 'Trabajo' : 'Trabajos'" />
        </v-toolbar-title>
        <v-spacer />
        <v-btn
          :disabled="isTrabajos"
          @click="$router.push({ name: 'peticion-recursos' })"
          v-if="is_peticion"
          class="primary"
        >
          Solicitar recurso
        </v-btn>
      </v-toolbar>
    </template>
    <template v-slot:item.modificado="{ item }">
      <v-chip :color="getColor(item.modificado)" dark>
        {{ item.modificado }}
      </v-chip>
    </template>
  </v-data-table>
</template>

<script>
export default {
  props: {
    mostrar_seleccionados: { type: Boolean, default: false },
    is_peticion: { type: Boolean, default: true }
  },
  data: () => ({
    headers: [
      { text: "Proyecto", value: "proyecto" },
      { text: "Concepto", value: "concepto" },
      { text: "Status", value: "status" },
      { text: "Inmueble", value: "inmueble.nombre" },
      { text: "Clave", value: "inmueble.clave" },
      { text: "Estado", value: "inmueble.estado" },
      { text: "Municipio", value: "inmueble.municipio" },
      { text: "Asignación", value: "crado" },
      { text: "Actualizado", value: "modificado" },
    ],
    singleSelect: false,
  }),
  computed: {
    cargando() { return this.$store.getters["MOD_trabajos/getCargando"]; },
    trabajos() { return this.$store.getters["MOD_trabajos/getTrabajos"]; },
    isTrabajos() { return this.trabajos.length ? false : true; },
    trabajos_seleccionados: {
      get(){ return this.$store.getters["MOD_trabajos/getTrabajosSeleccionados"] },
      set(nuevoValor){
        const peticiones = []
        for(const trabajos_seleccionado of nuevoValor){
          peticiones.push({ 'trabajo': trabajos_seleccionado.id, 'monto': 0.0})
        }
        this.$store.commit("MOD_pagos/setVerificarPeticiones", peticiones)
        this.$store.commit("MOD_trabajos/setSeleccionados", nuevoValor)
      }
    },
  },
  methods: {
    holgura(modificado) {
      var date1 = new Date(modificado);
      var date2 = new Date();
      var datestring =
        date2.getFullYear() +
        "-" +
        (date2.getMonth() + 1) +
        "-" +
        date2.getDate();
      var date3 = new Date(datestring);
      var holgura = (date3.valueOf() - date1.valueOf()) / 86400000;
      if (this.blue < holgura) this.blue = holgura;
      return holgura;
    },
    getColor(modificado) {
      if (this.holgura(modificado) > 30) return "red";
      else if (this.holgura(modificado) > 15) return "orange";
      else return "green";
    },
  },
};
</script>
