import axios from '@/api'

import { INITIALISER_FUNCTION, LOGOUT_FUNCTION } from '@/store/constants'
import { localStorage2json, json2localStorage } from '@/utils/data/store'


export default {
  namespaced: true,

  state: {
    notificaciones: [],
    numeroNotificaciones: 0,
    numeroNotificacionesDiferencia: 0
  },

  mutations: {
    MUT_setNotificaciones(state, notificaciones) {
      json2localStorage('notificaciones', notificaciones)
      state.notificaciones = notificaciones
    },
    limpiarNotificaciones(state) {
      json2localStorage('notificaciones', [])
      state.notificaciones = []
    }
  },

  actions: {
    [INITIALISER_FUNCTION]({ dispatch }) {
      dispatch('iniciarNotificaciones');
    },
    [LOGOUT_FUNCTION]({ commit }) {
      commit('limpiarNotificaciones')
    },
    iniciarNotificaciones({ commit },) {
      const notificaciones = localStorage2json('notificaciones')
      if (notificaciones) {
        commit('MUT_setNotificaciones', notificaciones)
      } else {
        commit('MUT_setNotificaciones', [])
      }
    },
    async traerNotificaciones({ commit }, { id_usuario }) {
      try {
        const { data } = await axios.instance.get(`/notificaciones/notificacion/${id_usuario}/`)
        const { items, message } = data
        commit('MUT_setNotificaciones', items)
        return { message }
      } catch (error) {
        const messageError = String(error.response.data.error)
        return { 'erorr': messageError }
      }
    },
    async actualizarNotificaciones(context, { id, user, is_read }) {
      try {
        const { data } = await axios.instance.put(`/notificaciones/notificacion/${id}/`, {
          'user': user,
          'is_read': is_read
        })
        const { message } = data
        return { message }
      } catch (error) {
        const messageError = String(error.response.data.error)
        return { 'erorr': messageError }
      }
    }
  },

  getters: {
    getNotificaciones: (state) => state.notificaciones,
    getNumeroNotificacionesDiferencia: (state) => state.numeroNotificacionesDiferencia
  }
}