<template>
  <v-row dense justify="center">
    <v-col cols="12" sm="8">
      <v-row >
        <v-col cols="12" sm="10" xs="10">
          <v-btn-toggle v-model="icon">
            <v-btn value="No disponible">
              <span class="hidden-sm-and-down">Asignado</span>
            </v-btn>
    
            <v-btn  value="En proceso">
              <span class="hidden-sm-and-down">Revisión</span>
            </v-btn>
    
            <v-btn  value="Concluido">
              <span class="hidden-sm-and-down">Concluido</span>
            </v-btn>
          </v-btn-toggle>
        </v-col>
      </v-row>
    </v-col>
    <v-col cols="12" sm="8">
      <tabla-trabajos-usuario />
    </v-col>
  </v-row>
</template>

<script>
import TablaTrabajos from '../../components/trabajo/ListarTrabajosUsuario.vue'
export default {
  data: () => ({
    headers: [
      { text: "Proyecto", value: "proyecto" },
      { text: "Concepto", value: "concepto" },
      { text: "Estado", value: "estado" },
      { text: "Inmueble", value: "inmueble.name" },
      { text: "Asignación", value: "crado" },
      { text: "Actualizado", value: "modificado" },
    ],
    icon: "No disponible",
  }),
  components: {
    "tabla-trabajos-usuario": TablaTrabajos,
  },
  created() {
    this.inicializarTrabajos();
  },
  computed: {
    username() { return this.$store.getters["MOD_autenticacion/getNombreUsuario"]; },
  },
  methods: {
    async inicializarTrabajos() {
      this.$store.commit("MOD_trabajos/cleanTrabajos")
      const { mensaje, estado, error } = await this.$store.dispatch( "MOD_trabajos/obtenerTrabajosUsuario",
        { username: this.username }
      );
      if (error) {
        this.$store.commit("alerta/addAlertas", { "estado": estado, "mensaje": mensaje, "error": error })
      }
    },
    verTrabajo(value) {
      this.$store.commit("MOD_trabajos/setTrabajo", value);
      this.$router.push({
        name: "ArchivosTrabajo",
        params: {
          nombreUsuario: this.username,
        },
      });
    },
    setTrabajo({ peticion }) {
      const index = this.trabajos.indexOf(peticion);
      this.trabajos[index].formaPago = "re";
      console.log(this.trabajos[index], peticion.formaPago);
    },
  },
};
</script>

<style>
span {
  text-transform: none !important;
}
tbody tr:nth-of-type(odd) {
  background-color: rgba(76, 129, 253, 0.125);
}
</style>
