<template>
  <v-dialog
    v-model="dialog"
    width="500"
    persistent
    transition="dialog-top-transition"
  >
    <template v-slot:activator="{ on, attrs }">
      <v-btn small color="success" v-bind="attrs" v-on="on">
        <v-icon left>mdi-bank</v-icon>Crear
      </v-btn>
    </template>
    <v-card class="mx-auto" max-width="500">
      <v-card-title class="grey lighten-2">
        Crear nuevo cliente
        <v-spacer />
        <v-btn icon color="error" @click="cerrarDialogo()">
          <v-icon>mdi-close</v-icon>
        </v-btn>
      </v-card-title>
      <v-card-text>
        <v-form
          ref="formulario"
          v-model="validacion"
          lazy-validation
          class="container"
        >
          <v-text-field
            v-model="nombre"
            label="Nombre cliente *"
            :rules="[
              (v) => !!v || 'El nombre del cliente o razón social es necesario',
            ]"
            dense
            outlined
            clearable
            filled
          />

          <v-text-field
            v-model="rfc"
            label="RFC"
            dense
            clearable
            outlined
            filled
          />

          <v-textarea
            v-model="direccion"
            label="Dirección"
            dense
            outlined
            clearable
            filled
          />
        </v-form>
        <v-card-actions class="justify-end">
          <v-btn @click="cerrarDialogo()" color="error" outlined
            >Cancelar</v-btn
          >
          <v-btn
            @click="crear()"
            color="success"
            :disabled="!validacion"
            outlined
            >Crear</v-btn
          >
        </v-card-actions>
      </v-card-text>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  data: () => ({
    validacion: false,
    nombre: null,
    rfc: null,
    direccion: null,
    dialog: false,
    uploadDialog: true,
    multiple: true,
  }),
  methods: {
    async crear() {
      if (this.$refs.formulario.validate()) {
        const { nombre, rfc, direccion } = this;
        const { error, mensaje, estado } = await this.$store.dispatch(
          "MOD_clientes/nuevo",
          {
            nombre,
            rfc,
            direccion,
          }
        );
        if (!error) {
          this.$refs.formulario.reset();
        }
        this.$store.commit("alerta/addAlertas", {
          estado: estado,
          mensaje: mensaje,
          error: error,
        });
      }
    },
    cerrarDialogo() {
      this.$refs.formulario.reset();
      this.dialog = false;
    },
  },
};
</script>
