        </v-card-text>
      </v-card>
    </v-col>
  </v-row>
</template>

<script>
import FormularioCrearInmueblesArchivo from "../../components/Inmueble/FormularioCrearInmueblesArchivo.vue";
import TablaCrearInmueblesArchivo from "../../components/TablaCrearInmueblesArchivo.vue";

