<template>
  <v-container class="py-0 fill-height">
    <v-btn
      icon
      @click="$router.push({ name: 'Home' })"
    >
      <v-icon x-large>mdi-home-circle</v-icon>
    </v-btn>
    <menu-navegacion-principal />
    <v-spacer />
    <notificaciones />
    <ver-perfil-y-mas />
  </v-container>
</template>

<script>
import MenuNavegacionPrincipal from "../components/encabezado/MenuNavegacionPrincipal.vue";
import Notificaciones from "../components/encabezado/Notificaciones.vue";
import VerPerfilYMas from "../components/encabezado/VerPerfilMas.vue";
export default {
  components: {
    notificaciones: Notificaciones,
    "menu-navegacion-principal": MenuNavegacionPrincipal,
    "ver-perfil-y-mas": VerPerfilYMas,
  },
  computed: {
    rolUsuario() {
      return this.$store.getters["MOD_autenticacion/getUserPermissions"];
    },
    isAdministrador() {
      return this.rolUsuario == "Administrador" || this.rolUsuario == "Coordinador"
        ? true
        : false;
    },
  },
};
</script>
