<template>
  <v-container>
    <v-row>
      <v-col cols="12" sm="4">
        <v-card class="mx-auto" max-width="500">
          <v-list>
            <v-list-item-group v-model="model">
              <v-list-item v-for="(item, i) in items" :key="i">
                <v-list-item-content>
                  <v-list-item-title v-text="item.text"></v-list-item-title>
                </v-list-item-content>
              </v-list-item>
            </v-list-item-group>
          </v-list>
        </v-card>
      </v-col>
      <v-col cols="12" sm="8">
        <v-card>
          <v-container>
            <v-text-field
              v-model="nombre"
              label="Nombre"
              required
            ></v-text-field>

            <v-text-field
              v-model="apellidos"
              label="Apellidos"
              required
            ></v-text-field>

            <v-btn
              class="mr-4"
              type="submit"
              @click="
                actualizarPerfil({ nombre: nombre, apellidos: apellidos })
              "
            >
              Actualizar perfil
            </v-btn>
          </v-container>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
  data: () => ({
    items: [
      {
        text: "Pefil",
      },
      {
        text: "Cuenta",
      },
      {
        text: "Seguridad de la cuenta",
      },
      {
        text: "Email",
      },
    ],
    model: 1,
  }),
  computed: {
    nombre: {
      get() {
        return this.$store.getters["MOD_autenticacion/obtenerNombreUsuario"];
      },
      set(value) {
        this.nombre = value;
      },
    },
    apellidos: {
      get() {
        return this.$store.getters["MOD_autenticacion/obtenerApellidosUsuario"];
      },
      set(value) {
        this.apellidos = value;
      },
    },
  },
  methods: {
    actualizarPerfil({ nombre, apellidos }) {
      if (
        apellidos != null &&
        apellidos != "" &&
        nombre != null &&
        nombre != ""
      )
      alert("En mantenimiento por el momento.");
    },
  },
};
</script>
